import React, { FC, ReactElement, useEffect, useState } from "react";
import Alert from "../../components/Alert/Alert";
import Button from "../../components/Buttons/Button";
import TextInput from "../../components/Inputs/TextInput";
import Loading from "../../components/Loading/Loading";
import Modal from "../../components/Modal/Modal";
import {
  FC_Search_Available_Candidates,
  FC_UpdateSchoolTrainingRequest,
  InterfaceTrainingRequestSchools,
  TrainingInterface,
} from "../actions/training.settings.action";

type UpdateSchoolNumberProps = {
  close: () => void;
  success: (data: InterfaceTrainingRequestSchools) => void;
  school: InterfaceTrainingRequestSchools;
  remaining: number;
  training: TrainingInterface;
};

const UpdateSchoolNumber: FC<UpdateSchoolNumberProps> = ({
  close,
  school,
  remaining,
  training,
  success,
}): ReactElement => {
  const [number, setNumber] = useState(0);
  const [laoding, setLoading] = useState(false);
  const [error, setError] = useState("");
  const [loadingCheckAvaliable, setLoadingCheckAvailable] =
    useState<boolean>(true);
  const [availableTeachers, setAvailableTeachers] = useState<number>(0);

  useEffect(() => {
    setNumber(school.total);

    if (loadingCheckAvaliable) {
      // selectSchoolCheckAvailable();
      if (training) {
        setAvailableTeachers(0);
        setLoadingCheckAvailable(true);
        FC_Search_Available_Candidates(
          {
            criteria: training.trainee_criteria,
            end_date: training.end_date + "",
            start_date: training.start_date + "",
            school_code: school.school_id + "",
            sub_area_id: training.sub_area_id + "",
            topic: training.topic_id + "",
            training_id: training.id + "",
          },
          (status, data, err) => {
            if (!status) setError(`Error: ${err}`);

            setLoadingCheckAvailable(false);
            if (data) setAvailableTeachers(data.total);
          }
        );
      }
    }
  }, [school, loadingCheckAvaliable, training]);

  // const selectSchoolCheckAvailable = () => {
  //   if (training) {
  //     setAvailableTeachers(0);
  //     setLoadingCheckAvailable(true);
  //     FC_Search_Available_Candidates(
  //       {
  //         criteria: training.trainee_criteria,
  //         end_date: training.end_date + "",
  //         start_date: training.start_date + "",
  //         school_code: school.school_id + "",
  //         sub_area_id: training.sub_area_id + "",
  //         topic: training.topic_id + "",
  //         training_id: training.id + "",
  //       },
  //       (status, data, err) => {
  //         if (!status) setError(`Error: ${err}`);

  //         setLoadingCheckAvailable(false);
  //         if (data) setAvailableTeachers(data.total);
  //       }
  //     );
  //   }
  // };

  const updateTotalNumber = () => {
    setError("");
    if (number <= 0) return setError("The total must not be less than zero");

    if (number > availableTeachers)
      return setError(
        `The the maximum available candidates on this school is ${availableTeachers}`
      );

    if (number > remaining)
      return setError(
        `The remaining candidates on this training is ${remaining}`
      );

    setLoading(true);
    FC_UpdateSchoolTrainingRequest(
      {
        training_id: training.id,
        total: number,
        school_code: school.school_code,
      },
      (status, msg) => {
        if (status) {
          success({ ...school, total: number });
        } else {
          setError(`Error: ${msg}`);
          setLoading(false);
        }
      }
    );
  };

  return (
    <Modal
      backDrop={true}
      close={close}
      backDropClose={false}
      displayClose={true}
      padding={{
        title: true,
        body: undefined,
        footer: undefined,
      }}
      title={"Update school number"}
    >
      <div className="border-t p-4">
        {error !== "" && <Alert theme="danger">{error}</Alert>}
        {loadingCheckAvaliable ? (
          <div className="text-center">
            <section className="inline-block">
              <Loading />
            </section>

            <h2 className="text-lg pt-2">Checking available on school</h2>
          </div>
        ) : (
          <>
            <div className="border p-2 rounded">
              <label className="text-sm opacity-30">School</label>
              <h2 className="text-lg font-bold">
                {school.school_code} {school.school_name}
              </h2>
            </div>

            <div className="py-2">
              <TextInput
                onChange={(e) => setNumber(+e.target.value)}
                value={number}
                error={""}
                label={"Set Teachers you need"}
                placeholder={"Set Teachers you need"}
                max={availableTeachers}
                name={""}
                disabled={laoding}
              />
            </div>
            <div className="text-right">
              <Button
                type="button"
                loading={laoding}
                onClick={updateTotalNumber}
              >
                Update numbers
              </Button>
            </div>
          </>
        )}
      </div>
    </Modal>
  );
};
export default UpdateSchoolNumber;
