import React, { FC, ReactElement, useEffect, useState } from "react";
import { DistrictList, FC_GetDistrictList } from "../../actions";
import Alert from "../../components/Alert/Alert";
import Button from "../../components/Buttons/Button";
import InputSelect from "../../components/Inputs/InputSelect";
import SearchInput from "../../components/Inputs/SearchInput";
import TextInput from "../../components/Inputs/TextInput";
import Loading from "../../components/Loading/Loading";
import Modal from "../../components/Modal/Modal";
import { search } from "../../utils/functions";
import {
  FC_AddCenterToTraining,
  FC_GetCenterAssets,
  FC_GetCenterAssets_Interface,
  FC_GetCenters,
  GroupItem,
  TrainingCenter,
} from "../actions/training.settings.action";

type AddSettingsToTrainingProps = {
  close: () => void;
  centers: TrainingCenter[];
  successHandler: (center: GroupItem) => void;
  training_id: string;
  selectedCenter: GroupItem | null;
};

interface Alert_Interface {
  target:
    | "MAIN-ERROR"
    | "MAIN-SUCCESS"
    | "ROOM-TYPE"
    | "CAPACITY"
    | "CENTER-NAME"
    | null;
  msg: string;
}

const defaultAlert: Alert_Interface = {
  target: null,
  msg: "",
};
const AddCenterToTraining: FC<AddSettingsToTrainingProps> = ({
  close,
  training_id,
  successHandler,
  centers,
  selectedCenter,
}): ReactElement => {
  const [loading_centers, setLoadingCenters] = useState(false);
  const [data_centers, setData] = useState<TrainingCenter[]>([]);
  const [districts, setDistricts] = useState<DistrictList[]>([]);
  const [loading_districts, setLoadingDistricts] = useState(true);
  const [error, setError] = useState<Alert_Interface>(defaultAlert);
  const [selected_district, setDistrict] = useState<string>("");
  const [selected_center_type, setCenterType] = useState<string>("");
  const [search_center, setSearchCenter] = useState<string>("");
  const [loading_add_center, setLoadingAddCenter] = useState(false);
  const [selected_center, setSelectedCenter] = useState<TrainingCenter | null>(
    null
  );

  const [loading_assets, setLoadingAssets] = useState(false);
  const [center_assets, setCenterAssets] =
    useState<FC_GetCenterAssets_Interface | null>(null);

  // const [group_name, setGroupName] = useState("");
  const [selected_group_type_id, setSelectedGroupTypeId] = useState("");
  const [capacity, setCapacity] = useState(10);

  useEffect(() => {
    FC_GetDistrictList((status, data, msg) => {
      if (!status) setError({ target: "MAIN-ERROR", msg: `Error: ${msg}` });
      setLoadingDistricts(false);
      setDistricts(data);
    });

    if (selectedCenter !== undefined && selectedCenter !== null) {
      // selectedCenter
      setDistrict(selectedCenter.district_id + "");
      setSelectedCenter({
        center_id: selectedCenter.center_id + "",
        center_type_id: selectedCenter.center_type_id + "",
        center_type_name: selectedCenter.center_name + "",
        district_id: selectedCenter.district_id + "",
        district_name: selectedCenter.district_name + "",
        id: selectedCenter.center_id + "",
        name: selectedCenter.center_name + "",
        status: "ACTIVE",
      });

      if (!center_assets) getCenterInfo();
    }
  }, [selectedCenter, center_assets]);

  const addCenterHandler = () => {
    try {
      setError(defaultAlert);

      if (selected_center === null)
        return setError({ target: "MAIN-ERROR", msg: "No selected center" });

      //* Validations
      if (selected_group_type_id === "")
        return setError({
          target: "ROOM-TYPE",
          msg: "Please select a group type",
        });

      if (capacity <= 0)
        return setError({
          target: "CAPACITY",
          msg: "Please provide a number",
        });

      setLoadingAddCenter(true);
      FC_AddCenterToTraining(
        {
          training_id: training_id,
          center_id: selected_center.id.toString(),
          group_id: selected_group_type_id.toString(),
          number_of_trainees: capacity.toString(),
          name: "",
        },
        (status, group_id, err) => {
          if (status === true && group_id) {
            setLoadingAddCenter(false);
            setError({
              target: "MAIN-SUCCESS",
              msg: `a Group has been added to this training`,
            });

            setSearchCenter("");

            successHandler({
              id: group_id,
              number_of_trainees: capacity,
              training_id: training_id,
              center_id:
                selected_center?.center_id?.toString() ||
                selected_center?.id?.toString(),
              center_name:
                selected_center?.center_name || selected_center?.name,
              center_type_id: +selected_center?.id,
              center_type_name: selected_center?.center_type_name || "",
              district_id: +selected_center?.district_id,
              district_name: selected_center?.district_name,
              group_id: group_id,
              group_name: "",
              status: "ACTIVE",
            });
            setSelectedCenter(null);

            setTimeout(() => {
              setError(defaultAlert);
              close();
            }, 2000);
          } else {
            setLoadingAddCenter(false);
            setError({
              target: "MAIN-ERROR",
              msg: `Error: ${err}`,
            });
          }
        }
      );
    } catch (error: any) {
      console.log("THis is the error");

      console.log({ ...error });
    }
  };

  const getCenters = (district_id: string) => {
    setLoadingCenters(true);
    if (!center_assets) getCenterInfo();
    setData([]);

    FC_GetCenters(district_id, (status, res_data, msg) => {
      setLoadingCenters(false);
      if (!status) setError({ msg: `Error: ${msg}`, target: "MAIN-ERROR" });
      setData(res_data);
    });
  };

  const getCenterInfo = () => {
    setLoadingAssets(true);
    FC_GetCenterAssets((status, data, msg) => {
      if (status && data) {
        setCenterAssets(data);
      } else {
        setError({
          target: "MAIN-ERROR",
          msg: `Error: ${msg}`,
        });
      }
      setLoadingAssets(false);
    });
  };

  let centerResults: TrainingCenter[] = data_centers || [];

  if (selected_center_type.toString() !== "")
    centerResults = centerResults.filter(
      (el) => el.center_type_id.toString() === selected_center_type.toString()
    );

  centerResults = search(centerResults || [], search_center, {
    name: true,
    center_type_name: true,
    district_name: true,
  });

  return (
    <div>
      <Modal
        backDrop={true}
        close={close}
        backDropClose={false}
        displayClose={true}
        title={"Add center the training"}
        padding={{
          title: true,
          body: undefined,
          footer: undefined,
        }}
      >
        <div className="border-t p-2 px-3">
          {error.target === "MAIN-SUCCESS" && (
            <Alert theme="success">{error.msg}</Alert>
          )}
          {error.target === "MAIN-ERROR" && (
            <Alert theme="danger">{error.msg}</Alert>
          )}

          {selected_center === null && (
            <div className="lg:flex gap-3 border-b">
              <div className="flex-1">
                <InputSelect
                  onChange={(e) => {
                    setDistrict(e.target.value);
                    getCenters(e.target.value);
                  }}
                  value={selected_district}
                  error={""}
                  label={"Select a district"}
                  placeholder={"select district"}
                  name={""}
                  data={[
                    { title: "", value: "" },
                    ...districts.map((el) => ({
                      title: el.district_name,
                      value: el.district_code,
                    })),
                  ]}
                  disabled={loading_districts || loading_centers}
                />
              </div>

              <div className="flex-1">
                {data_centers && center_assets?.center_types && (
                  <InputSelect
                    onChange={(e) => {
                      setCenterType(e.target.value);
                    }}
                    value={selected_center_type}
                    error={""}
                    label={"Select a center type"}
                    placeholder={"select center type"}
                    name={""}
                    data={[
                      { title: "All", value: "" },
                      ...center_assets?.center_types.map((el) => ({
                        title: el.name || "",
                        value: el.id || "",
                      })),
                    ]}
                    disabled={loading_districts || loading_centers}
                  />
                )}
              </div>
            </div>
          )}

          {loading_districts && (
            <div className="py-2 text-center mx-auto text-gray-600 animate__animated animate__fadeIn animate__infinite">
              Loading assets ...
            </div>
          )}
          <div className="mx-auto">
            {loading_assets && (
              <div className="py-2 text-center">
                <Loading />
              </div>
            )}
          </div>

          {selected_district && (
            <>
              {selected_center === null && (
                <div className="p-1 rounded bg-white border animate__animated animate__zoomIn animate__faster">
                  <div className="flex items-center border-b px-2">
                    <h4 className="text-xl flex-1 font-bold ">
                      Training centers
                    </h4>
                    <div className="flex-1">
                      <SearchInput
                        onChange={(e) => setSearchCenter(e.target.value)}
                        value={search_center}
                        placeholder={"Search for a center"}
                        name={""}
                        size="sm"
                      />
                    </div>
                  </div>
                  <div className="h-96 border p-1 rounded">
                    <table className="w-full">
                      <thead>
                        <tr>
                          <th className="border p-1">#</th>
                          <th className="border p-1">Center</th>
                          <th className="border p-1">Center type</th>
                          <th className="border p-1">District</th>
                          <th className="border p-1 w-4"></th>
                        </tr>
                      </thead>
                      <tbody>
                        {loading_centers ? (
                          <tr>
                            <td className="py-4 text-center" colSpan={12}>
                              <div className="mx-auto text-center">
                                <Loading />
                              </div>
                            </td>
                          </tr>
                        ) : data_centers.length <= 0 ? (
                          <tr>
                            <td
                              className="py-6 text-center text-2xl text-gray-400"
                              colSpan={12}
                            >
                              <div className="mx-auto">No centers found!</div>
                            </td>
                          </tr>
                        ) : centerResults.length <= 0 ? (
                          <tr>
                            <td
                              className="py-6 text-center text-2xl text-gray-400"
                              colSpan={12}
                            >
                              <div className="mx-auto">
                                No results for your search found!
                              </div>
                            </td>
                          </tr>
                        ) : (
                          centerResults.map((el, i) => {
                            return (
                              <tr key={i} className="hover:bg-accent-400">
                                <td className="border p-1 text-center">
                                  {i + 1}
                                </td>
                                <td className="border p-1 font-bold pl-2">
                                  {el.center_name}
                                </td>
                                <td className="border p-1">
                                  {el.center_type_name}
                                </td>
                                <td className="border p-1">
                                  {el.district_name}
                                </td>
                                <td className="border p-1">
                                  <Button
                                    size="sm"
                                    theme="accent-light"
                                    onClick={setSelectedCenter.bind(this, el)}
                                  >
                                    Select
                                  </Button>
                                </td>
                              </tr>
                            );
                          })
                        )}
                      </tbody>
                    </table>
                  </div>
                </div>
              )}
            </>
          )}

          {selected_center !== null && (
            <div className="animate__animated animate__zoomIn animate__faster">
              <div className="p-1 flex w-full border">
                <div className="flex-1 ">
                  <div className="flex items-center pr-2 pb-1 mb-1 border-b">
                    <section className=" p-2 flex-1">
                      <label className="text-sm text-gray-700">
                        Center name:
                      </label>
                      <h2 className="font-bold">{selected_center.name}</h2>
                    </section>
                    <section>
                      <Button
                        theme="danger-light"
                        onClick={setSelectedCenter.bind(this, null)}
                      >
                        Change center
                      </Button>
                    </section>
                  </div>

                  <div className="flex">
                    <section className=" p-2 flex-1">
                      <label className="text-sm text-gray-700">
                        Center type:
                      </label>
                      <h2 className="font-bold">
                        {selected_center.center_type_name}
                      </h2>
                    </section>
                    <section className=" p-2 flex-1">
                      <label className="text-sm text-gray-700">District:</label>
                      <h2 className="font-bold">
                        {selected_center.district_name}
                      </h2>
                    </section>
                  </div>
                </div>
              </div>

              <div className="flex gap-4">
                <div className="flex-1">
                  <InputSelect
                    onChange={(e) => setSelectedGroupTypeId(e.target.value)}
                    value={selected_group_type_id}
                    error={error.target === "ROOM-TYPE" ? error.msg : ""}
                    label={"Select training center"}
                    placeholder={"Room"}
                    name={""}
                    data={[
                      { title: "", value: "" },
                      ...(center_assets?.center_room_type.map((el) => ({
                        title: el.name?.toString(),
                        value: el.id?.toString() || "",
                      })) || []),
                    ]}
                    disabled={loading_add_center}
                  />
                </div>
                <div className="flex-1">
                  <TextInput
                    onChange={(e) => setCapacity(+e.target.value)}
                    value={capacity}
                    error={error.target === "CAPACITY" ? error.msg : ""}
                    label={"Capacity"}
                    placeholder={"Room capacity"}
                    name={""}
                    disabled={loading_add_center}
                  />
                </div>
              </div>
              {/* <div className="">
                <div className="flex-1">
                  <TextInput
                    onChange={(e) => setGroupName(e.target.value)}
                    value={group_name}
                    error={error.target === "CENTER-NAME" ? error.msg : ""}
                    label={"Group name"}
                    placeholder={"Intore"}
                    name={""}
                    disabled={loading_add_center}
                  />
                </div>
              </div> */}

              <div className="pt-3 text-right">
                <Button
                  loading={loading_add_center}
                  onClick={addCenterHandler}
                  size="lg"
                >
                  Add center to the training
                </Button>
              </div>
            </div>
          )}
        </div>
        {selected_center === null && (
          <div className="py-3 border-t">
            <h4 className="text-xl text-center font-bold">Can't find it?</h4>
            <p className="text-center text-sm text-gray-500 pb-2">
              If you can't find the item you are looking for <br />
              Contact REB, they will add it here
            </p>
          </div>
        )}
      </Modal>
    </div>
  );
};
export default AddCenterToTraining;
