import React, { FC, ReactElement, useState, useEffect } from "react";
import { v4 as UUID } from "uuid";
import {
  MdAttachFile,
  MdDriveFolderUpload,
  MdOutlineLink,
  MdDelete,
  MdFileUpload,
  MdInsertDriveFile,
} from "react-icons/md";
import Button from "../../components/Buttons/Button";
import DashboardContainer from "../../components/DashboardContainer/DashboardContainer";
import Heading from "../../components/Heading/Heading";
import Modal, { ModalSize } from "../../components/Modal/Modal";
import {
  MaterialLinks,
  TrainingInterface,
  UPLOAD_AGENDA,
} from "../actions/training.settings.action";
import TextInput from "../../components/Inputs/TextInput";
import Alert from "../../components/Alert/Alert";
import { setAxiosToken } from "../../utils/AxiosToken";
import axios from "axios";
import { errorToText } from "../../utils/errors";
import BottomNavigation from "./BottomNavigation";
import PdfViewer from "../../components/PdfViewer/PdfViewer";
import { AGENDA_PLANS_PATH } from "../../config/app.config";

type Step4ResourceProps = {
  training: TrainingInterface | null;
  submitTrainingResource: (materials: MaterialLinks[]) => void;
  loading_materials: boolean;
  uploadFileSuccess: (file: string) => void;
  prevStep?: () => void;
  stake_holder_id: string;
};

interface AlertInterface {
  target: "MAIN-ERROR" | "MAIN-SUCCESS" | null;
  msg: string;
}

const defaultAlert: AlertInterface = {
  target: null,
  msg: "",
};

const Step4Resource: FC<Step4ResourceProps> = ({
  training,
  submitTrainingResource,
  loading_materials,
  uploadFileSuccess,
  prevStep,
  stake_holder_id,
}): ReactElement => {
  const [links, setLinks] = useState<MaterialLinks[]>([]);
  const [add_link, setAddLink] = useState(false);
  const [error, setError] = useState<AlertInterface>(defaultAlert);

  const [title, setTitle] = useState("");
  const [description, setDescription] = useState("");
  const [link_name, setLinkName] = useState("");

  const [loading_upload, setLoadingUpload] = useState(false);
  const [files, setFiles] = useState<any>(null);
  const [upload_progress, setUploadProgress] = useState<number>(0);
  const [open_file, setOpenFile] = useState<boolean>(false);

  useEffect(() => {
    if (training?.material_links) {
      setLinks(training?.material_links);
    }
  }, [training]);

  const CleanError = () =>
    setTimeout(() => {
      setError(defaultAlert);
    }, 3000);

  const createRessource = () => {
    if (title.length <= 1) {
      CleanError();
      return setError({ target: "MAIN-ERROR", msg: "Please provide a name" });
    }
    if (link_name.length <= 1) {
      CleanError();
      return setError({ target: "MAIN-ERROR", msg: "Please provide a link" });
    }

    let NewLink: MaterialLinks = {
      description: description,
      title: title,
      url: link_name,
      id: UUID(),
    };

    submitTrainingResource([...links, NewLink]);
    setLinks([...links, NewLink]);
    setAddLink(false);

    setTitle("");
    setDescription("");
    setLinkName("");
  };

  const removeRessource = (id: string) => {
    if (!window.confirm("Are you sure you want to remove this resource??"))
      return;
    let new_links = links.filter((el) => el.id?.toString() !== id);

    submitTrainingResource(new_links);
    setLinks(new_links);
    setAddLink(false);
  };

  const submitFile = (e: any) => {
    e.preventDefault();

    if (files.length <= 0) {
      setError({
        target: "MAIN-ERROR",
        msg: "Pease provide a file",
      });

      CleanError();
      return;
    }

    FC_UploadUserDocumentAPI();
  };

  // upload data
  const FC_UploadUserDocumentAPI = async () => {
    setLoadingUpload(true);
    try {
      var formData = new FormData();

      formData.append("development_partner_id", stake_holder_id);
      formData.append("id", training?.id + "");
      formData.append("agenda_file_name", files[0]);
      // add the subcategories

      setAxiosToken();
      await axios.patch(UPLOAD_AGENDA, formData, {
        onUploadProgress: (progressEvent: any) => {
          let number_percentage: number =
            (+progressEvent?.loaded / +progressEvent.total) * 100;
          setUploadProgress(number_percentage);
        },
      });

      setFiles(null);
      setUploadProgress(0);
      setError({
        target: "MAIN-SUCCESS",
        msg: "Document uploaded successfully!",
      });

      uploadFileSuccess(files[0].name);

      setTimeout(() => {
        setError(defaultAlert);
      }, 3000);

      setLoadingUpload(false);
    } catch (error) {
      setError({
        target: "MAIN-ERROR",
        msg: `Error: ${errorToText(error)}`,
      });
      setLoadingUpload(false);
    }
  };

  /* function body */
  return (
    <>
      {error.target === "MAIN-SUCCESS" && (
        <Alert theme="success" fixed>
          {error.msg}
        </Alert>
      )}
      {error.target === "MAIN-ERROR" && (
        <Alert theme="danger" fixed>
          {error.msg}
        </Alert>
      )}
      {add_link && (
        <Modal
          backDrop={true}
          close={() => setAddLink(false)}
          backDropClose={true}
          displayClose={true}
          padding={{
            title: true,
            body: undefined,
            footer: undefined,
          }}
          title="Add training Material(s) "
        >
          <div className="border-t p-2">
            <section className="p-2">
              <TextInput
                onChange={(e) => setTitle(e.target.value)}
                value={title}
                error={""}
                label={"Title"}
                placeholder={""}
                disabled={loading_materials}
                name={""}
              />
            </section>
            <section className="p-2">
              <TextInput
                onChange={(e) => setDescription(e.target.value)}
                value={description}
                error={""}
                label={"Description"}
                placeholder={""}
                name={""}
                disabled={loading_materials}
              />
            </section>
            <section className="p-2">
              <TextInput
                onChange={(e) => setLinkName(e.target.value)}
                value={link_name}
                error={""}
                label={"Material link from e-learning"}
                placeholder={""}
                name={""}
                disabled={loading_materials}
              />
            </section>
            <div className="p-2 text-right">
              <Button
                type="button"
                onClick={createRessource}
                loading={loading_materials}
              >
                Add material
              </Button>
            </div>
          </div>
        </Modal>
      )}

      {open_file && (
        <Modal
          widthSizeClass={ModalSize.maxWidth}
          backDrop={true}
          close={() => setOpenFile(false)}
          backDropClose={true}
          displayClose={true}
          padding={{
            title: true,
            body: undefined,
            footer: undefined,
          }}
          title="Agenda file preview"
        >
          <div className="border-t">
            <PdfViewer
              file_url={`${AGENDA_PLANS_PATH}${training?.agenda_file_name}`}
              class_name={"w-full h-100vh"}
              frame_title={""}
              setLoadingFile={() => {}}
            />
          </div>
        </Modal>
      )}

      <DashboardContainer>
        <div className="p-2 border-b flex items-center">
          <section className="text-5xl pr-2">
            <MdAttachFile />
          </section>
          <section>
            <Heading size="md">Add training resource</Heading>
            <p className="text-gray-500">
              Add the training plan and the resources
            </p>
          </section>
        </div>

        <div className="p-2 flex w-full gap-4">
          <div className="flex-1 py-4 text-center flex items-center bg-gray-100 rounded">
            {loading_upload === true ? (
              <div className="text-center w-full">
                <section className="text-8xl text-gray-500 pb-2 mx-auto animate__animated animate__fadeIn animate__infinite animate__slow">
                  <MdFileUpload className="inline-block" />
                </section>

                <div className="px-5">
                  <div className="p-1  bg-blue-200 w-full rounded-full text-center">
                    <section
                      style={{ width: `${upload_progress}%` }}
                      className=" rounded-full w-6 bg-blue-600 text-center text-white py-0.5 font-bold text-xs"
                    >
                      {upload_progress.toFixed(0)}%
                    </section>
                  </div>
                  <h2 className="pt-2 font-bold text-lg text-gray-400 text-center">
                    Uploading agenda
                  </h2>
                </div>
              </div>
            ) : (
              <div className="text-center w-full">
                <section>
                  {training?.agenda_file_name &&
                  training.agenda_file_name.length >= 1 ? (
                    <div>
                      <div className="flex border m-2 mx-4 bg-white p-1">
                        <section className="text-6xl text-blue-500 pb-2 mx-auto">
                          <MdInsertDriveFile className="inline-block" />
                        </section>
                        <section className="text-left pt-2 flex-1 pl-2">
                          <h4 className="text-lg font-bold">
                            {training.agenda_file_name}
                          </h4>

                          <button
                            onClick={() => setOpenFile(true)}
                            className="p-2 px-0 text-blue-700 font-bold hover:underline"
                          >
                            Preview agenda document
                          </button>
                        </section>
                      </div>

                      {/* <h2 className="text-2xl font-bold">Upload document</h2> */}
                      <p className="text-gray-500 mb-3">
                        You can replace the agenda document by uploading the new
                        one
                      </p>
                    </div>
                  ) : (
                    <section>
                      <section className="text-8xl text-gray-500 pb-2 mx-auto">
                        <MdDriveFolderUpload className="inline-block" />
                      </section>
                      <h2 className="text-2xl font-bold">
                        Upload agenda document
                      </h2>
                      <p className="text-gray-500 mb-3">
                        <b>1MB max</b>
                      </p>
                    </section>
                  )}
                  <form onSubmit={submitFile}>
                    <div className="flex gap-3 bg-blue-100 items-center p-2 m-2 rounded">
                      <div className="flex-1 ">
                        <input
                          type={"file"}
                          className="p-2 border rounded bg-white w-full text-xl"
                          onChange={(e) => setFiles(e.target.files)}
                        />
                      </div>
                      <Button theme="primary" size="lg">
                        Upload
                      </Button>
                    </div>
                  </form>{" "}
                </section>
              </div>
            )}
          </div>
          <div className="flex-1 rounded border p-2">
            <section className="text-center border-b p-2 flex items-center">
              <section className="text-2xl p-2 inline-block rounded-lg bg-blue-100 text-blue-600">
                <MdOutlineLink className="block" />
              </section>
              <h3 className="text-xl flex-1 font-bold text-left pl-2">
                E-learning material links
              </h3>
              <section>
                <Button theme="success-light" onClick={() => setAddLink(true)}>
                  Add material
                </Button>
              </section>
            </section>

            <div className="p-3">
              {links.map((el, i) => (
                <section
                  key={i}
                  className="p-2 border mb-2 rounded items-center border-blue-600 bg-blue-50 bg-opacity-40 hover:bg-blue-50 flex"
                >
                  <section className="text-4xl pr-1 text-blue-600">
                    <MdOutlineLink className="block" />
                  </section>
                  <section className="flex-1">
                    <h2 className="font-bold hover:text-blue-500 hover:underline">
                      {el.title}
                    </h2>
                    <p className="text-xs text-gray-600">{el.description}</p>
                  </section>
                  <section>
                    <Button
                      theme="danger-light"
                      size="sm"
                      onClick={removeRessource.bind(this, el.id + "")}
                      disabled={loading_materials}
                    >
                      <h3 className="text-2xl">
                        <MdDelete />
                      </h3>
                    </Button>
                  </section>
                </section>
              ))}
            </div>
          </div>
        </div>
      </DashboardContainer>
      <BottomNavigation>
        <div className="text-right  flex-1 flex">
          {prevStep && (
            <section className="flex-1 text-left">
              <Button theme="default" onClick={prevStep} loading={false}>
                Previous
              </Button>
            </section>
          )}

          {true && (
            <section className="flex-1 text-right">
              <Button loading={false}>Next</Button>
            </section>
          )}
        </div>
      </BottomNavigation>
    </>
  );
};
export default Step4Resource;
