import React, { FC, ReactElement } from "react";
import { MdOutlineLocationOn } from "react-icons/md";
import Button from "../../components/Buttons/Button";

import {
  FC_UpdateRoomCapacity_Interface,
  GroupItem,
  TrainingTrainers,
} from "../actions/training.settings.action";
import { NoCenterPlaceholder } from "./Placeholder";
import TrainingGroupItem from "./TrainingGroupItem";

type TrainingCenterItemProps = {
  addGroupHandler: (center: GroupItem) => void;
  center: GroupItem;
  selected: string;
  selectHandler: (arg: string) => void;
  trainers: TrainingTrainers[];
  groups: GroupItem[];
  deleteCenterGroup: (el: { training_id: string; id: string }) => void;
  delete_group_loading: string | null;
  updatedRoomCapacity: (data: FC_UpdateRoomCapacity_Interface) => void;
  setAddTrainer: (group: GroupItem) => void;
  loading_delete_trainer: string | null;
  removeTrainerHandler: (id: string) => void;
};

const TrainingCenterItem: FC<TrainingCenterItemProps> = ({
  addGroupHandler,
  center,
  selected,
  selectHandler,
  trainers,
  groups,
  deleteCenterGroup,
  updatedRoomCapacity,
  delete_group_loading,
  setAddTrainer,
  loading_delete_trainer,
  removeTrainerHandler,
}): ReactElement => {
  let GroupsList: GroupItem[] = groups.filter(
    (el) => el.center_id + "" === center.center_id + ""
  );

  let total_trainees: number = 0;
  for (const el of GroupsList) {
    total_trainees += +el.number_of_trainees;
  }

  return (
    <>
      <div className=" rounded-lg overflow-hidden hover:border-yellow-700 bg-white border mb-3 items-center">
        <div className="lg:flex sm:grid sm:grid-cols-2 w-full items-center py-2">
          <div className=" text-yellow-700 pl-2">
            <label className="text-4xl">
              <MdOutlineLocationOn />
            </label>
          </div>

          <div className="flex-1 border-r pl-2">
            <label className="text-gray-500 text-sm">Center type</label>
            <h4 className="text-bold text-lg font-bold -mt-1">
              {center?.center_type_name}
            </h4>
          </div>

          <div className="flex-1 border-r pl-2">
            <label className="text-gray-500 text-sm">Center name</label>
            <h4 className="text-bold text-lg font-bold -mt-1">
              {center?.center_name}
            </h4>
          </div>

          <div className="flex-1 border-r pl-2">
            <label className="text-gray-500 text-sm">District</label>
            <h4 className="text-bold text-lg font-bold -mt-1">
              {center?.district_name}
            </h4>
          </div>

          <div className="pl-2 w-36 border-r">
            <label className="text-gray-500 text-sm">Center capacity</label>
            <h4 className="text-bold text-lg font-bold -mt-1">
              {total_trainees}
            </h4>
          </div>

          <div className="pl-2 w-36">
            <label className="text-gray-500 text-sm">Total Trainers</label>
            <h4 className="text-bold text-lg font-bold -mt-1">40</h4>
          </div>

          <div className="text-right flex pr-2">
            {selected.toString() === center.id.toString() ? (
              <div className="flex gap-2">
                <Button
                  theme="accent-light"
                  onClick={addGroupHandler.bind(this, center)}
                  disabled={delete_group_loading !== null}
                >
                  Add a group
                </Button>
              </div>
            ) : (
              <Button
                theme="primary-light"
                onClick={() => selectHandler(center.id.toString())}
                disabled={delete_group_loading !== null}
              >
                Open
              </Button>
            )}
            <div className="text-right pl-2 pr-2">
              {selected.toString() === center.id.toString() && (
                <Button
                  theme="default"
                  onClick={() => selectHandler("")}
                  disabled={delete_group_loading !== null}
                >
                  Close
                </Button>
              )}
            </div>
          </div>
        </div>

        {selected.toString() === center.id.toString() && (
          <div className="p-4 bg-gray-300 bg-opacity-25 border-t animate__animated animate__zoomIn animate__faster">
            {GroupsList.length >= 1 && (
              <h2 className="text-xl font-bold mb-2">Groups list</h2>
            )}

            {GroupsList.map((group, i) => (
              <TrainingGroupItem
                key={i}
                addTraineeToGroup={setAddTrainer}
                updatedRoomCapacity={updatedRoomCapacity}
                group={group}
                trainers={trainers}
                deleteCenterGroup={deleteCenterGroup}
                delete_group_loading={delete_group_loading}
                loading_delete_trainer={loading_delete_trainer}
                removeTrainerHandler={removeTrainerHandler}
              />
            ))}

            {GroupsList.length <= 0 && (
              <NoCenterPlaceholder click={addGroupHandler.bind(this, center)} />
            )}
          </div>
        )}
      </div>
    </>
  );
};
export default TrainingCenterItem;
