import React, { FC, ReactElement, useState } from "react";
import { MdSupervisorAccount } from "react-icons/md";
import Alert from "../../components/Alert/Alert";
import Button from "../../components/Buttons/Button";
import TextInput from "../../components/Inputs/TextInput";
import {
  // FC_RemoveTraineeToGroup,
  FC_UpdateRoomCapacity,
  FC_UpdateRoomCapacity_Interface,
  GroupItem,
  TrainingTrainers,
} from "../actions/training.settings.action";

type TrainingGroupItemProps = {
  addTraineeToGroup: (group: GroupItem) => void;
  group: GroupItem;
  trainers: TrainingTrainers[];
  deleteCenterGroup: (el: { training_id: string; id: string }) => void;
  delete_group_loading: string | null;
  updatedRoomCapacity: (data: FC_UpdateRoomCapacity_Interface) => void;
  // addTrainerHandler: (user: any) => void;
  removeTrainerHandler: (id: any) => void;
  loading_delete_trainer: string | null;
};

interface AlertInterface {
  target: "MAIN-ERROR" | "MAIN-SUCCESS" | "TRAINING-CAPACITY" | null;
  msg: string;
}

const defaultAlert: AlertInterface = {
  target: null,
  msg: "",
};
const TrainingGroupItem: FC<TrainingGroupItemProps> = ({
  addTraineeToGroup,
  group,
  trainers,
  deleteCenterGroup,
  delete_group_loading,
  updatedRoomCapacity,
  loading_delete_trainer,
  removeTrainerHandler,
}): ReactElement => {
  const [capacity, setCapacity] = useState<number>(0);
  const [loading_update_capacity, setLoadingUpdateCapacity] =
    useState<boolean>(false);

  const [alert, setAlert] = useState<AlertInterface>(defaultAlert);
  const [trainer_institution, setTGroupUpdate] = useState<GroupItem | null>(
    null
  );

  let trainersList = trainers.filter(
    (el) => el.group_id.toString() === group?.group_id?.toString()
  );
  // let trainersList = trainers;

  const cleanError = () => setAlert(defaultAlert);
  const cleanErrorTimeout = () =>
    setTimeout(() => {
      setAlert(defaultAlert);
    }, 2000);

  const updateCenterCapacity = () => {
    if (!trainer_institution) {
      cleanError();
      cleanErrorTimeout();
      return setAlert({
        target: "TRAINING-CAPACITY",
        msg: "No room selected",
      });
    }

    if (capacity <= 0) {
      cleanError();
      cleanErrorTimeout();
      return setAlert({
        target: "TRAINING-CAPACITY",
        msg: "The capacity must be greater than zero",
      });
    }

    setLoadingUpdateCapacity(true);
    FC_UpdateRoomCapacity(
      {
        id: trainer_institution.id.toString(),
        number_of_trainees: capacity,
        training_id: trainer_institution.training_id,
      },
      (status, err) => {
        setLoadingUpdateCapacity(false);
        if (status) {
          setAlert({
            target: "MAIN-SUCCESS",
            msg: "Data Updated successfully",
          });
          updatedRoomCapacity({ ...group, number_of_trainees: capacity });
          setTGroupUpdate(null);
        } else {
          setAlert({
            target: "MAIN-ERROR",
            msg: `Error: ${err}`,
          });
        }
        cleanErrorTimeout();
      }
    );
  };

  console.log({ trainersList, group, trainers });

  return (
    <>
      {alert.target === "MAIN-ERROR" && (
        <Alert theme="danger">{alert.msg}</Alert>
      )}
      {alert.target === "MAIN-SUCCESS" && (
        <Alert theme="success">{alert.msg}</Alert>
      )}
      <div className="p-2 rounded mb-2 hover:border-yellow-500 hover:border-2 hover:border bg-white flex border border-rounded">
        <div className="flex w-44">
          {trainer_institution !== null && (
            <div className="p-2 rounded items-center border mr-1 animate__animated animate__zoomIn animate__faster">
              <div className="flex">
                <section className="flex-1 ">
                  <h2 className="text-lg font-bold">Update</h2>
                </section>

                <section>
                  <Button
                    size="sm"
                    theme="danger-light"
                    onClick={setTGroupUpdate.bind(this, null)}
                    disabled={loading_update_capacity}
                  >
                    close
                  </Button>
                </section>
              </div>
              <div className="pt-2">
                <TextInput
                  onChange={(e) => setCapacity(+e.target.value)}
                  value={capacity}
                  error={""}
                  label={"Trainee capacity"}
                  placeholder={""}
                  name={""}
                />{" "}
                <section className="text-right pt-2">
                  <Button
                    onClick={updateCenterCapacity}
                    type="button"
                    theme="success"
                    loading={loading_update_capacity}
                  >
                    Update capacity
                  </Button>
                </section>
              </div>
            </div>
          )}

          {trainer_institution === null && (
            <div className="pl-2">
              <div className="flex-1 border-b pb-1">
                <label className="text-gray-500 text-sm">Group:</label>
                <h4 className="text-bold text-lg font-bold -mt-1">
                  {group.group_name}
                </h4>
              </div>

              <div className="flex-1 border-b py-1">
                <label className="text-gray-500 text-sm">Center capacity</label>
                <h4 className="text-bold text-lg font-bold -mt-1">
                  {group.number_of_trainees}
                </h4>
              </div>

              <div className="pt-2 pr-2">
                <Button
                  theme="primary-light"
                  onClick={() => {
                    setTGroupUpdate(group);
                    setCapacity(+group.number_of_trainees);
                  }}
                  className="w-full mb-2"
                >
                  Edit capacity
                </Button>

                <Button
                  theme="danger-light"
                  className="w-full"
                  onClick={() =>
                    deleteCenterGroup({
                      training_id: group.training_id,
                      id: group.id.toString(),
                    })
                  }
                  disabled={delete_group_loading !== null}
                  loading={
                    delete_group_loading?.toString() === group.id.toString()
                      ? true
                      : false
                  }
                >
                  Remove room
                </Button>
              </div>
            </div>
          )}
        </div>
        <div className="flex-1 rounded p-2 border">
          <div className="flex items-center p-0.5 px-1 mb-1 rounded bg-gray-200">
            <section className="flex-1 pl-2">
              <h3 className="text-lg font-bold">Trainers</h3>
            </section>
            <section className="text-right">
              <Button onClick={addTraineeToGroup.bind(this, group)}>
                Add a trainer to Group: {group.group_name}
              </Button>
            </section>
          </div>

          <table className="border w-full">
            <thead>
              <tr className="text-left">
                <th className="p-1.5 border">#</th>
                <th className="p-1.5 border">NID</th>
                <th className="p-1.5 border">Names</th>
                <th className="p-1.5 border">Phone number</th>
                <th className="p-1.5 border">Email</th>
                <th className="p-1.5 border w-10"></th>
              </tr>
            </thead>
            <tbody>
              {trainersList.length <= 0 && (
                <tr>
                  <td className="py-3 text-center" colSpan={20}>
                    <section className="mx-auto text-center text-6xl opacity-50">
                      <MdSupervisorAccount className="inline" />
                    </section>

                    <h2 className="font-bold opacity-50">
                      Add trainer{`(s)`} to this room
                    </h2>
                  </td>
                </tr>
              )}
              {trainersList.map((trainer, i) => (
                <tr key={i} className="hover:bg-yellow-100 text-sm">
                  <td className="p-2 border">{i + 1}</td>
                  <td className="p-2 border">{trainer.nid}</td>
                  <td className="p-2 border">
                    {trainer.first_name} {trainer.last_name}
                  </td>
                  <td className="p-2 border">{trainer.phone_numbers}</td>
                  <td className="p-2 border">{trainer.email}</td>
                  <td className="p-0.5 text-center border">
                    ,
                    <Button
                      size="sm"
                      theme="danger-light"
                      type="button"
                      onClick={removeTrainerHandler.bind(
                        this,
                        trainer.user_id.toString()
                      )}
                      disabled={loading_delete_trainer !== null}
                      loading={
                        loading_delete_trainer?.toString() ===
                        trainer.user_id.toString()
                      }
                    >
                      Remove
                    </Button>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </div>
    </>
  );
};
export default TrainingGroupItem;
