import React, { FC, ReactElement, useState } from "react";
import Alert from "../../components/Alert/Alert";
import Button from "../../components/Buttons/Button";
import DashboardContainer from "../../components/DashboardContainer/DashboardContainer";
import InputSelect from "../../components/Inputs/InputSelect";
import SearchInput from "../../components/Inputs/SearchInput";
import Loading from "../../components/Loading/Loading";
import Modal, { ModalSize } from "../../components/Modal/Modal";
import { search } from "../../utils/functions";
import {
  FC_AddTrainerToGroup,
  FC_GetTrainersByTrainingInstitution,
  FC_RemoveTrainerToTraining,
  FC_UpdateRoomCapacity_Interface,
  GroupItem,
  TrainingInterface,
  TrainingTrainers,
} from "../actions/training.settings.action";
import BottomNavigation from "./BottomNavigation";
import { Step2PLaceholder } from "./Placeholder";
import TrainingCenterItem from "./TrainingCenterItem";
import TrainingCenterNav from "./TrainingCenterNav";

interface AlertInterface {
  target: "MAIN-ERROR" | "MAIN-SUCCESS" | null;
  msg: string;
}

const defaultAlert: AlertInterface = {
  target: null,
  msg: "",
};

type StepTwoCentersTrainersProps = {
  training: TrainingInterface;
  addCenter: () => void;
  delete_group_loading: string | null;
  addGroupHandler: (center: GroupItem) => void;
  deleteCenterGroup: (el: { training_id: string; id: string }) => void;
  updatedRoomCapacity: (data: FC_UpdateRoomCapacity_Interface) => void;
  nextStep?: () => void;
  prevStep?: () => void;
  successSubmitTrainer: (data: TrainingTrainers) => void;
  removeTrainerSuccess: (id: string) => void;
};

const StepTwoCentersTrainers: FC<StepTwoCentersTrainersProps> = ({
  training,
  addCenter,
  deleteCenterGroup,
  delete_group_loading,
  addGroupHandler,
  updatedRoomCapacity,
  nextStep,
  prevStep,
  successSubmitTrainer,
  removeTrainerSuccess,
}): ReactElement => {
  const [error, setError] = useState<AlertInterface>(defaultAlert);
  const [search_center, setSearchCenter] = useState("");
  const [selected, setSelected] = useState("");
  const [selected_training_institution, setSelectedTrainingInstitution] =
    useState("");
  const [open_add_trainer, setAddTrainer] = useState<GroupItem | null>(null);
  const [search_trainer_results, setSearchTrainersResults] =
    useState<string>("");

  const [loading_trainers, setLoadingTrainer] = useState<boolean>(false);
  // const [changed, setChanged] = useState<boolean>(false);
  const [loading_add_user_to_training, setLoadingAddUserToTraining] =
    useState<TrainingTrainers | null>(null);
  const [trainers_list, setTrainersList] = useState<TrainingTrainers[]>([]);
  const [loading_delete_trainer, setLoading_delete_trainer] = useState<
    string | null
  >(null);

  const removeTrainerHandler = (id: string) => {
    if (
      !window.confirm(
        "Are you sure you want remove this trainer to the training"
      )
    )
      return;

    console.log("Delete: ", id);

    setLoading_delete_trainer(id);
    FC_RemoveTrainerToTraining(id, training.id, (status, err) => {
      if (status) {
        setError({
          target: "MAIN-SUCCESS",
          msg: `User has been removed to this training!`,
        });

        removeTrainerSuccess(id);
      } else {
        setError({
          target: "MAIN-ERROR",
          msg: `Error: ${err}`,
        });
      }

      ClearError();
      setLoading_delete_trainer(null);
    });
  };

  let centers_list: GroupItem[] = [];
  for (const group_item of training.groups || []) {
    let found = centers_list.find(
      (el) => el.center_id?.toString() === group_item.center_id + ""
    );
    if (found === undefined) centers_list.push(group_item);
  }

  let TrainersResults: TrainingTrainers[] = search(
    trainers_list,
    search_trainer_results,
    {
      nid: true,
      first_name: true,
      full_name: true,
      last_name: true,
      phone_numbers: true,
      email: true,
    }
  );

  let TrainingCenters: GroupItem[] = search(centers_list || [], search_center, {
    name: true,
    district_name: true,
    center_type_name: true,
  });

  const ClearError = () => {
    setTimeout(() => {
      setError(defaultAlert);
    }, 3000);
  };

  const GetTrainers = (id: string) => {
    setSelectedTrainingInstitution(id);
    setTrainersList([]);

    if (id === null) {
      setTrainersList([]);
      setError(defaultAlert);
      return;
    }
    setLoadingTrainer(true);
    FC_GetTrainersByTrainingInstitution(
      {
        end_date: training.end_date + "",
        start_date: training.start_date + "",
        training_institution_id: id,
      },
      (status, data, err) => {
        if (!status) {
          setError({ target: "MAIN-ERROR", msg: `Error: ${err}` });
          ClearError();
        }
        setLoadingTrainer(false);
        setTrainersList(data);
      }
    );
  };

  const AddTrainerToTraining = (user: TrainingTrainers) => {
    if (
      !window.confirm(
        `Are you sure you want to add ${user.full_name} to this training?`
      )
    )
      return;

    setLoadingAddUserToTraining(user);
    FC_AddTrainerToGroup(
      {
        training_id: training.id + "",
        training_to_group_id: open_add_trainer?.id + "",
        user_id: user.user_id,
      },
      (status, err) => {
        if (status) {
          setError({
            target: "MAIN-SUCCESS",
            msg: `Candidate has been added to the training`,
          });

          successSubmitTrainer({
            email: user.email,
            group_id: open_add_trainer?.group_id + "",
            id: open_add_trainer?.id + "",
            phone_numbers: user.phone_numbers,
            training_to_group_id: open_add_trainer?.id + "",
            status: "ACTIVE",
            user_id: user.user_id,
            first_name: user.first_name,
            full_name: user.full_name,
            last_name: user.last_name,
            nid: user.nid,
            staff_code: user.staff_code,
            training_id: training.id,
          });

          // setChanged(false);
        } else {
          setError({
            target: "MAIN-ERROR",
            msg: `Error: ${err}`,
          });
        }
        setLoadingAddUserToTraining(null);
        ClearError();
      }
    );
  };

  let trainer_category = [
    { title: "", value: "" },
    ...(training.trainer_institution
      ? training.trainer_institution.map((el) => ({
          title: el.name + "",
          value: el.id + "",
        }))
      : []),
  ];

  return (
    <>
      {error.target === "MAIN-SUCCESS" && (
        <Alert theme="success" fixed>
          {error.msg}
        </Alert>
      )}
      {error.target === "MAIN-ERROR" && (
        <Alert theme="danger" fixed>
          {error.msg}
        </Alert>
      )}

      {open_add_trainer !== null && (
        <Modal
          backDrop={true}
          close={() => setAddTrainer(null)}
          backDropClose={false}
          displayClose={true}
          widthSizeClass={ModalSize.large}
          padding={{
            title: true,
            body: undefined,
            footer: undefined,
          }}
          title="Add trainer to training"
        >
          {loading_add_user_to_training !== null ? (
            <div className="py-6 text-center border-t">
              <section className="inline-block">
                <Loading />
              </section>

              <h2 className="pt-3 text-xl">
                Adding <b>{loading_add_user_to_training.full_name}</b> to this
                training
              </h2>
            </div>
          ) : (
            <div className="border-t p-4">
              <div className="flex w-full gap-2 items-center">
                <section>
                  <InputSelect
                    onChange={(e) => GetTrainers(e.target.value)}
                    value={selected_training_institution}
                    error={""}
                    label={"Training institution"}
                    placeholder={""}
                    name={""}
                    data={trainer_category}
                    disabled={loading_trainers}
                  />
                </section>

                <section className="flex-1 pt-3">
                  <SearchInput
                    onChange={(e) => setSearchTrainersResults(e.target.value)}
                    value={search_trainer_results}
                    placeholder={""}
                    name={""}
                  />
                </section>
              </div>

              {loading_trainers && (
                <div className="text-center py-4">
                  <Loading />
                </div>
              )}

              {selected_training_institution === "" && (
                <div className="py-5 text-center border-t">
                  <h2 className="text-2xl text-gray-400 ">
                    Please select an institution, <br /> in order to get the
                    list of trainers
                  </h2>
                </div>
              )}

              {!loading_trainers && selected_training_institution !== "" && (
                <table className="border w-full">
                  <thead>
                    <tr>
                      <th className="p-2 border">#</th>
                      <th className="p-2 border">Nid</th>
                      <th className="p-2 border">Names</th>
                      <th className="p-2 border">Email</th>
                      <th className="p-2 border">Phone</th>
                      <th className="p-2 border w-10"></th>
                    </tr>
                  </thead>
                  <tbody>
                    {TrainersResults.length <= 0 && (
                      <tr>
                        <td className="py-5 text-center" colSpan={12}>
                          <h2 className="text-2xl text-gray-600">
                            No results found!
                          </h2>
                        </td>
                      </tr>
                    )}

                    {TrainersResults.map((el, i) => (
                      <tr key={i}>
                        <td className="p-2 border">{i + 1}</td>
                        <td className="p-2 border">{el.nid}</td>
                        <td className="p-2 border">{el.full_name}</td>
                        <td className="p-2 border">{el.email}</td>
                        <td className="p-2 border">{el.phone_numbers}</td>
                        <td className="p-1 border">
                          <Button
                            size="sm"
                            onClick={AddTrainerToTraining.bind(this, el)}
                          >
                            Open
                          </Button>
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              )}

              {/* {trainers_list.length <= 0 &&
                loading_trainers === false &&
                selected_training_institution !== "" && (
                  <div className="py-8 text-center">
                    <h2 className="text-4xl font-bold text-gray-400">
                      No trainers found!
                    </h2>
                  </div>
                )} */}
            </div>
          )}
        </Modal>
      )}

      <TrainingCenterNav
        search={search_center}
        clickAction={addCenter}
        setSearch={setSearchCenter}
        total_centers={training.centers?.length}
        total_groups={training.groups?.length}
        total_trainees={1000}
        total_trainers={training.trainers?.length}
      />

      <DashboardContainer transparent className="-mt-7">
        <div className="">
          <div className="">
            {centers_list && centers_list.length <= 0 && (
              <Step2PLaceholder click={() => {}} />
            )}

            {TrainingCenters.map((center, i) => (
              <TrainingCenterItem
                key={i}
                updatedRoomCapacity={updatedRoomCapacity}
                deleteCenterGroup={deleteCenterGroup}
                addGroupHandler={addGroupHandler}
                center={center}
                selectHandler={(e) => setSelected(e)}
                selected={selected}
                trainers={training.trainers || []}
                groups={training.groups ? training.groups : []}
                delete_group_loading={delete_group_loading}
                setAddTrainer={(group) => setAddTrainer(group)}
                loading_delete_trainer={loading_delete_trainer}
                removeTrainerHandler={removeTrainerHandler}
              />
            ))}

            {/* <div className="text-center pt-2">
              <Button onClick={() => setNext(true)}>Add a center</Button>
            </div> */}
          </div>
        </div>
      </DashboardContainer>

      <BottomNavigation>
        <div className="text-right  flex-1 flex">
          {prevStep && (
            <section className="flex-1 text-left">
              <Button theme="default" onClick={prevStep} loading={false}>
                Previous
              </Button>
            </section>
          )}

          {nextStep && (
            <section className="flex-1 text-right">
              <Button loading={false} onClick={nextStep}>
                Next
              </Button>
            </section>
          )}
        </div>
      </BottomNavigation>
    </>
  );
};
export default StepTwoCentersTrainers;
