import React, { FC, ReactElement } from "react";

type CardTrainingRequestProps = {
  number: number;
  title: string;
  description: string;
  theme: "blue" | "green" | "gray" | "yellow";
  active: boolean;
  priority: boolean;
  onClick: () => void;
};

const CardTrainingRequest: FC<CardTrainingRequestProps> = ({
  number,
  title,
  description,
  theme,
  active,
  priority,
  onClick,
}): ReactElement => {
  /* function body */
  return (
    <div
      onClick={onClick}
      className={`p-1 lg:p-1  lg:border-t-transparent lg:rounded-t-md cursor-pointer lg:border-b-4 flex text-center lg:text-left flex-row items-center lg:border-${theme}-500 ${
        active === true ? `bg-${theme}-100` : ""
      } ${priority === false ? "opacity-60" : ""}`}
    >
      <section
        className={`p-1 lg:p-1 rounded-full border-4 ${
          theme === "yellow" ? `bg-${theme}-500` : `bg-${theme}-500`
        } 0 ${
          theme === "yellow" ? `border-${theme}-600` : `border-${theme}-200`
        }  ${
          theme === "yellow" ? "text-black" : "text-white"
        } sm:px-2 lg:w-12 lg:h-12 flex items-center text-center`}
      >
        <h3 className="text-center w-full lg:text-2xl font-bold ">{number}</h3>
      </section>
      <section className="pl-2 text-left">
        <h3 className="lg:text-lg font-bold">{title}</h3>
        <p className="text-gray-400 -mt-1 text-sm">{description}</p>
      </section>
    </div>
  );
};

export default CardTrainingRequest;
