import React, { FC, ReactElement, useEffect, useState } from "react";
import { MdDateRange, MdGroups } from "react-icons/md";
import Button from "../../components/Buttons/Button";
import DashboardContainer from "../../components/DashboardContainer/DashboardContainer";
import TextArea from "../../components/Inputs/TextArea";
import TextInput from "../../components/Inputs/TextInput";
import SelectCustom from "../../components/SelectCustom/SelectCustom";
import {
  FC_GetSettingType,
  TrainingSettings_Interface,
  TRNG_SUB_AREA_VIEW_URL,
  TRNG_TOPICS_VIEW_URL,
} from "../actions/training.settings.action";
import BottomNavigation from "./BottomNavigation";
import SelectGroup from "./SelectGroup";

type StepOneBasicInfoProps = {
  sub_area_id: string;
  successData: () => void;
  trainee_category: TrainingSettings_Interface[];
  trainer_institutions: TrainingSettings_Interface[];
  removeTraineeCategory: (id: string | number) => void;
  removeTrainerInstitution: (id: string | number) => void;
  addTraineeCategory: () => void;
  addTrainerInstitution: () => void;
  loadingTraineeCategory: boolean;
  loadingTrainerInstitution: boolean;
  loadingTraineeCategoryRemove: null | string | number;
  loadingTrainersInstitutionRemove: null | string | number;
  loadingStepOneSave: boolean;

  // State
  step_one__title: string;
  step_one__description: string;
  step_one__start_date: string;
  step_one__end_date: string;
  step_one__sub_area: string;
  step_one__budget: string;
  step_one__topic: string;
  setStepOne__title: (value: string) => void;
  setStepOne__description: (value: string) => void;
  setStepOne__start_date: (value: string) => void;
  setStepOne__end_date: (value: string) => void;
  setStepOne__sub_area: (value: string) => void;
  setStepOne__topic: (value: string) => void;
  setStepOne__budget: (value: string) => void;
};

interface ALert {
  target:
    | "TITLE"
    | "DESCRIPTION"
    | "START_DATE"
    | "END_DATE"
    | "SUB_AREA"
    | "TOPICS"
    | "BUDGET"
    | "MAIN_SUCCESS"
    | "MAIN_ERROR"
    | "TRAINING_INSTITUTION"
    | "TRAINEE_CATEGORY"
    | "MAIN_ERROR"
    | null;
  msg: string;
}

const defaultAlert: ALert = {
  target: null,
  msg: "",
};
const StepOneBasicInfo: FC<StepOneBasicInfoProps> = ({
  sub_area_id,
  successData,
  trainee_category,
  trainer_institutions,
  removeTraineeCategory,
  removeTrainerInstitution,
  loadingTraineeCategoryRemove,
  loadingTrainersInstitutionRemove,
  addTraineeCategory,
  addTrainerInstitution,
  loadingStepOneSave,

  // default input
  step_one__title,
  step_one__description,
  step_one__start_date,
  step_one__end_date,
  step_one__sub_area,
  step_one__budget,
  setStepOne__title,
  setStepOne__description,
  setStepOne__start_date,
  setStepOne__end_date,
  setStepOne__sub_area,
  setStepOne__budget,
  setStepOne__topic,
  step_one__topic,
}): ReactElement => {
  const [subAreaList, setSubAreaList] = useState<TrainingSettings_Interface[]>(
    []
  );
  const [topicsList, setTopicsList] = useState<TrainingSettings_Interface[]>(
    []
  );
  const [loadingSubArea, setLoadingSubArea] = useState(true);
  const [loadingTopics, setLoadingTopics] = useState(true);

  const [subarea_open, setSubArea_open] = useState(false);
  const [topics_open, setTopic_open] = useState(false);

  let selectedSubArea = subAreaList.find(
    (elm) => elm.id + "" === step_one__sub_area + ""
  );
  let selectedTopic = topicsList.find(
    (elm) => elm.id + "" === step_one__topic + ""
  );

  const [alert, setAlert] = useState<ALert>(defaultAlert);

  useEffect(() => {
    //** Load a sub-area */
    if (loadingSubArea === true && subAreaList.length === 0) {
      setLoadingSubArea(false);
      FC_GetSettingType(TRNG_SUB_AREA_VIEW_URL, (status, data, err) => {
        if (status === false)
          setAlert({ target: "SUB_AREA", msg: `Error: ${err}` });
        setSubAreaList(data);
        setLoadingSubArea(false);
      });

      FC_GetSettingType(TRNG_TOPICS_VIEW_URL, (status, data_topics, err) => {
        if (status === false)
          setAlert({ target: "TOPICS", msg: `Error: ${err}` });
        setTopicsList(data_topics);
        setLoadingTopics(false);
      });
    }

    //** Update the data */
  }, [sub_area_id, loadingSubArea, subAreaList.length]);

  const clearError = () => setTimeout(() => setAlert(defaultAlert), 3000);

  const submitBasicInfo = (e: any) => {
    e.preventDefault();

    if (step_one__title.length <= 4) {
      clearError();
      return setAlert({
        target: "TITLE",
        msg: "Please provide the title",
      });
    }

    if (step_one__description.length <= 4) {
      clearError();
      return setAlert({
        target: "DESCRIPTION",
        msg: "Please provide the description",
      });
    }

    if (!selectedSubArea) {
      clearError();
      return setAlert({
        target: "SUB_AREA",
        msg: "Please select a sub-area",
      });
    }

    if (!selectedTopic) {
      clearError();
      return setAlert({
        target: "TOPICS",
        msg: "Please select a topic",
      });
    }

    if (+step_one__budget === 0) {
      clearError();
      return setAlert({
        target: "BUDGET",
        msg: "Budget is required",
      });
    }

    if (step_one__start_date === "") {
      clearError();
      return setAlert({
        target: "START_DATE",
        msg: "Please provide the start date",
      });
    }

    if (step_one__end_date === "") {
      clearError();
      return setAlert({
        target: "END_DATE",
        msg: "Please provide the end date",
      });
    }

    if (
      new Date(step_one__start_date).getTime() >
      new Date(step_one__end_date).getTime()
    ) {
      clearError();
      return setAlert({
        target: "END_DATE",
        msg: "This (end date) must be later than (start date)",
      });
    }

    if (step_one__end_date === "") {
      clearError();
      return setAlert({
        target: "END_DATE",
        msg: "Please provide the end date",
      });
    }

    if (trainer_institutions.length === 0) {
      clearError();
      return setAlert({
        target: "TRAINING_INSTITUTION",
        msg: "You must provide at least one institution",
      });
    }
    if (trainee_category.length === 0) {
      clearError();
      return setAlert({
        target: "TRAINEE_CATEGORY",
        msg: "You must provide at least one category",
      });
    }

    // * Submit the data on the server
    successData();
  };

  return (
    <>
      <form onSubmit={submitBasicInfo}>
        <DashboardContainer className="animate__animated animate__zoomIn animate__faster">
          <div className="p-2">
            <div className="pb-2">
              <TextInput
                onChange={(e) => {
                  setStepOne__title(e.target.value);
                  setAlert(defaultAlert);
                }}
                value={step_one__title}
                error={alert.target === "TITLE" ? alert.msg : ""}
                label={"Training Title"}
                placeholder={"Title of the training"}
                name={""}
                disabled={loadingStepOneSave}
              />
            </div>

            <div className="">
              <TextArea
                onChange={(e) => {
                  setStepOne__description(e.target.value);
                  setAlert(defaultAlert);
                }}
                value={step_one__description}
                error={alert.target === "DESCRIPTION" ? alert.msg : ""}
                label={"Description of the training"}
                placeholder={"Description of the task"}
                name={""}
                disabled={loadingStepOneSave}
              />
            </div>

            <div className="lg:flex gap-6 py-2 w-full">
              <div className="pb-2 flex-1">
                {!subarea_open && (
                  <section
                    onClick={() =>
                      !loadingStepOneSave && setSubArea_open(!subarea_open)
                    }
                    className="cursor-pointer"
                  >
                    <TextInput
                      onChange={(e) => {}}
                      value={`${
                        loadingSubArea
                          ? "Loading..."
                          : selectedSubArea
                          ? `${selectedSubArea?.name} - ( ${selectedSubArea?.intervention_area})`
                          : ""
                      }`}
                      error={alert.target === "SUB_AREA" ? alert.msg : ""}
                      label={"Select sub Area"}
                      placeholder={""}
                      name={""}
                      disabled={true}
                    />
                  </section>
                )}

                {subarea_open && (
                  <>
                    <span className="block text-gray-400">Select sub area</span>
                    <SelectCustom
                      loading={loadingSubArea}
                      id={"id"}
                      title={"name"}
                      close={() => setSubArea_open(!subarea_open)}
                      data={subAreaList}
                      click={(el_data: any) => {
                        setStepOne__sub_area(el_data.id);
                        setSubArea_open(!subarea_open);
                      }}
                    />
                  </>
                )}
              </div>

              {/* //* Topics list */}

              <div className="pb-2 flex-1">
                {!topics_open && (
                  <section
                    onClick={() =>
                      !loadingStepOneSave && setTopic_open(!topics_open)
                    }
                    className="cursor-pointer"
                  >
                    <TextInput
                      onChange={(e) => {}}
                      value={`${
                        loadingTopics
                          ? "Loading..."
                          : selectedTopic
                          ? `${selectedTopic?.name}`
                          : ""
                      }`}
                      error={alert.target === "TOPICS" ? alert.msg : ""}
                      label={"Select topic"}
                      placeholder={""}
                      name={""}
                      disabled={true}
                    />
                  </section>
                )}

                {topics_open && (
                  <>
                    <span className="text-gray-400 block">Select a topic</span>
                    <SelectCustom
                      loading={loadingTopics}
                      id={"id"}
                      title={"name"}
                      close={() => setTopic_open(!topics_open)}
                      data={topicsList}
                      click={(el_data: any) => {
                        setStepOne__topic(el_data.id);
                        setTopic_open(!topics_open);
                      }}
                    />
                  </>
                )}
              </div>

              {/* Training budget */}

              <div className="pb-2 flex-1">
                <TextInput
                  onChange={(e) => {
                    setStepOne__budget(e.target.value);
                    setAlert(defaultAlert);
                  }}
                  // type="number"
                  value={step_one__budget}
                  error={alert.target === "BUDGET" ? alert.msg : ""}
                  label={"Budget"}
                  placeholder={"Budget of the training"}
                  name={""}
                />
              </div>
            </div>

            <div className="border lg:p-2 lg:px-4 rounded bg-gray-50">
              <section className="flex items-center text-yellow-800">
                <MdDateRange className="text-3xl" />
                <h4 className="font-bold text-lg pl-1">Training dates</h4>
              </section>

              <div className="lg:flex gap-4">
                <section className="flex-1">
                  <TextInput
                    onChange={(e) => {
                      setStepOne__start_date(e.target.value);
                      setAlert(defaultAlert);
                    }}
                    value={step_one__start_date}
                    type="date"
                    error={alert.target === "START_DATE" ? alert.msg : ""}
                    label={"Start Date"}
                    placeholder={"Start date"}
                    name={""}
                    disabled={loadingStepOneSave}
                  />
                </section>

                <section className="flex-1">
                  <TextInput
                    onChange={(e) => {
                      setStepOne__end_date(e.target.value);
                      setAlert(defaultAlert);
                    }}
                    value={step_one__end_date}
                    type="date"
                    error={alert.target === "END_DATE" ? alert.msg : ""}
                    label={"End Date"}
                    placeholder={"End date"}
                    name={""}
                    disabled={loadingStepOneSave}
                  />
                </section>
              </div>
            </div>

            <div className="lg:flex py-4 lg:gap-4">
              {/* Assign training institution */}
              <SelectGroup
                Icon={MdGroups}
                title={"Assign training institution"}
                description={"Institution which will help to train"}
                data={trainer_institutions}
                create_btn_label="Add institution"
                createLading={false}
                updateLading={null}
                deleteLoading={loadingTrainersInstitutionRemove}
                onCreate={addTrainerInstitution}
                onDelete={removeTrainerInstitution}
                error={alert.target === "TRAINING_INSTITUTION" ? alert.msg : ""}
              />
              {/* Assign trainee to training */}
              <SelectGroup
                Icon={MdGroups}
                title={"Assign trainee category"}
                description={"Assign the type of users you are about to train"}
                data={trainee_category}
                create_btn_label="Add category"
                deleteLoading={loadingTraineeCategoryRemove}
                onCreate={addTraineeCategory}
                onDelete={removeTraineeCategory}
                error={alert.target === "TRAINEE_CATEGORY" ? alert.msg : ""}
              />
            </div>
          </div>
        </DashboardContainer>

        <BottomNavigation>
          <div className="text-right flex-1 ">
            <Button loading={loadingStepOneSave}>Save and Continue</Button>
          </div>
        </BottomNavigation>
      </form>
    </>
  );
};
export default StepOneBasicInfo;
