import React, { FC, ReactElement, useEffect, useState } from "react";
import Alert from "../../components/Alert/Alert";
import Button from "../../components/Buttons/Button";
import DashboardContainer from "../../components/DashboardContainer/DashboardContainer";
import SearchInput from "../../components/Inputs/SearchInput";
import Loading from "../../components/Loading/Loading";
import { DATE, search, shortString } from "../../utils/functions";
import {
  FC_DeleteSchoolRequestTraining,
  FC_GetSchoolsList,
  InterfaceTrainingRequestSchools,
  TrainingInterface,
} from "../actions/training.settings.action";
import AddSchool from "./AddSchool";
import UpdateSchoolNumber from "./UpdateSchoolNumber";

type RequestTrainingSchoolsProps = {
  close: () => void;
  training: TrainingInterface | null;
};

interface AlertInterface {
  target: "MAIN-ERROR" | "MAIN-SUCCESS" | null;
  msg: string;
}

const defaultAlert: AlertInterface = {
  target: null,
  msg: "",
};

const RequestTrainingSchools: FC<RequestTrainingSchoolsProps> = ({
  close,
  training,
}): ReactElement => {
  const [training_school, setTraining_school] = useState<
    InterfaceTrainingRequestSchools[]
  >([]);
  const [error, setError] = useState<AlertInterface>(defaultAlert);
  const [loading, setLoading] = useState<boolean>(true);
  const [search_text, setSearch] = useState<string>("");
  const [add_school, setAddSchool] = useState<boolean>(false);
  const [delete_loading, setDeleteLoading] = useState<string | null>(null);
  const [selectedUpdate, setSelectedUpdate] =
    useState<InterfaceTrainingRequestSchools | null>(null);

  useEffect(() => {
    if (loading)
      FC_GetSchoolsList(training?.id + "", (status, data, err) => {
        if (!status) setError({ msg: `Error: ${err}`, target: "MAIN-ERROR" });
        setLoading(false);
        setTraining_school(data);
      });
  }, [loading, training]);

  const UpdateSuccess = (data: InterfaceTrainingRequestSchools) => {
    setTraining_school(
      training_school.map((el) =>
        el.school_code === data.school_code ? data : el
      )
    );
    setError({
      target: "MAIN-SUCCESS",
      msg: "School total updated successfully",
    });
    clearError();
    setSelectedUpdate(null);
  };

  const successAddSchool = (data: InterfaceTrainingRequestSchools) => {
    setTraining_school([...training_school, data]);
  };

  const clearError = () =>
    setTimeout(() => {
      setError(defaultAlert);
    }, 2000);

  const DeleteSchoolOnTheList = (school: InterfaceTrainingRequestSchools) => {
    if (
      !window.confirm(
        "Are you sure you want to remove this school to the request list?"
      )
    )
      return;
    setDeleteLoading(school.school_code);
    FC_DeleteSchoolRequestTraining(
      school.school_code,
      training?.id + "",
      (status, err) => {
        if (status) {
          setTraining_school(
            training_school.filter(
              (el) =>
                el?.school_code?.toString() !== school.school_code.toString()
            )
          );
          setError({
            target: "MAIN-SUCCESS",
            msg: `The school has been removed to the list`,
          });
        } else {
          setError({
            target: "MAIN-ERROR",
            msg: `Error: ${err}`,
          });
        }
        clearError();
        setDeleteLoading(null);
      }
    );
  };

  if (!training)
    return (
      <DashboardContainer>
        <div className="py-5 text-center">
          <h3 className="py-2 font-bold text-2xl">No training found</h3>
          <Button theme="default" type="button" size="lg" onClick={close}>
            Back
          </Button>
        </div>
      </DashboardContainer>
    );

  let TrainingSchools: InterfaceTrainingRequestSchools[] = search(
    training_school,
    search_text,
    {
      school_name: true,
      district_name: true,
    }
  );

  let total_trainees = training.total_trainees || 0;
  let total_available = 0;
  let total_distributed = 0;
  for (const itm of training_school) {
    total_distributed += itm.total;
    total_available += +(training?.total_available_trainee || 0);
  }
  let total_remaining = total_trainees - total_distributed;

  return (
    <>
      {error.target === "MAIN-SUCCESS" && (
        <Alert theme="success" fixed>
          {error.msg}
        </Alert>
      )}
      {error.target === "MAIN-ERROR" && (
        <Alert theme="danger" fixed>
          {error.msg}
        </Alert>
      )}

      {add_school && training && (
        <AddSchool
          successHandler={successAddSchool}
          close={() => setAddSchool(false)}
          training={training}
          training_schools={training_school}
          remaining={total_remaining}
        />
      )}

      {selectedUpdate !== null && training && (
        <UpdateSchoolNumber
          school={selectedUpdate}
          close={setSelectedUpdate.bind(this, null)}
          success={UpdateSuccess}
          remaining={total_remaining}
          training={training}
        />
      )}

      <DashboardContainer>
        <div className="w-full border-b py-2 flex">
          <section>
            <Button theme="default" type="button" size="sm" onClick={close}>
              Back
            </Button>
          </section>
          <section className="px-2 flex-1">
            <h3 className="text-2xl font-bold text-gray-500">
              Request training schools
            </h3>
          </section>
        </div>

        <div className="flex items-center">
          <div className="p-2 flex-1" title={training.title + ""}>
            <label className="text-sm text-gray-500">Training title:</label>
            <h4 className="text-lg -mt-1 font-bold ">
              {shortString(training.title + "", 40)}
            </h4>
          </div>

          <section className="px-2">
            <label className="text-sm text-gray-400">Start date</label>
            <h3 className=" font-bold text-gray-500">
              {DATE(training.start_date)}
            </h3>
          </section>
          <section className="px-2 ">
            <label className="text-sm text-gray-400">End date</label>
            <h3 className=" font-bold text-gray-500">
              {DATE(training.start_date)}
            </h3>
          </section>
        </div>
        <div className="py-1 border-t pt-2 flex gap-3 pb-3">
          <section className="px-2 border rounded bg-blue-50 text-blue-700">
            <label className="text-sm text-gray-500">Total trainees</label>
            <h2 className="text-lg font-bold -mt-1">{total_trainees}</h2>
          </section>

          <section className="px-2 border rounded bg-blue-50 text-blue-700">
            <label className="text-sm text-gray-500">Total available</label>
            <h2 className="text-lg font-bold -mt-1">{total_available}</h2>
          </section>

          <section className="px-2 border rounded bg-blue-50 text-blue-700">
            <label className="text-sm text-gray-500">Total distributed</label>
            <h2 className="text-lg font-bold -mt-1">{total_distributed}</h2>
          </section>

          <section className="px-2 border rounded bg-blue-50 text-blue-700">
            <label className="text-sm text-gray-500">Total remaining</label>
            <h2 className="text-lg font-bold -mt-1">{total_remaining}</h2>
          </section>
        </div>
        {training.trainee_criteria && (
          <div className="gap-4 p-1 bg-gray-100 border rounded grid grid-cols-2">
            {/* {training.trainee_criteria.districts.length >= 1 && (
              <>
                <div className="border p-1 bg-white rounded">
                  <label className="text-sm px-2 text-gray-500">Training</label>
                  <div className=" -mt-1">
                    {training.trainee_criteria.districts.map((el, i) => (
                      <span
                        className="inline-block p-1 px-2 m-1 bg-blue-100 text-blue-700 text-sm rounded-full"
                        key={i}
                      >
                        {el.name}
                      </span>
                    ))}
                  </div>
                </div>
              </>
            )} */}
          </div>
        )}
      </DashboardContainer>

      <DashboardContainer>
        <div className="p-1 flex gap-2 items-center pb-2">
          <section className="flex-1">
            <SearchInput
              onChange={(e) => setSearch(e.target.value)}
              value={search_text}
              placeholder={"Search by a school name or district"}
              name={""}
              size="sm"
            />
          </section>

          <section className="pl-2 flex gap-2">
            {/* <Button onClick={() => setAddSchool(true)}>Add District</Button>
            <Button onClick={() => setAddSchool(true)}>Add Sector</Button> */}
            {/* <Button onClick={() => setAddSchool(true)}>
              Add Teachers or parents
            </Button> */}

            <Button onClick={() => setAddSchool(true)}>Request trainees</Button>
          </section>
        </div>
        <table className="border w-full">
          <thead>
            <tr className="text-left bg-gray-100">
              <th className="p-2 border">School code</th>
              <th className="p-2 border">School name</th>
              <th className="p-2 border">District name</th>
              <th className="p-2 border">Total</th>
              <th className="p-2 border">Available trainee</th>
              <th className="p-2 border"></th>
              {/* <th className="p-2 border"></th> */}
            </tr>
          </thead>
          <tbody>
            {training_school.length <= 0 && loading === false && (
              <tr>
                <td colSpan={12} className="py-5 text-center">
                  <h2 className="text-4xl text-gray-400">No data found</h2>
                </td>
              </tr>
            )}

            {TrainingSchools.length <= 0 &&
              loading === false &&
              training_school.length !== 0 && (
                <tr>
                  <td colSpan={12} className="py-5 text-center">
                    <h2 className="text-4xl text-gray-400">No results found</h2>
                  </td>
                </tr>
              )}

            {loading && (
              <tr>
                <td colSpan={12} className="py-5 text-center">
                  <section className="section">
                    <Loading />
                  </section>
                </td>
              </tr>
            )}

            {TrainingSchools.map((el, i) => (
              <tr key={i}>
                <td className="p-2 border">{el.school_code}</td>
                <td className="p-2 border">{el.school_name}</td>
                <td className="p-2 border">{el.district_name}</td>
                <td className="p-2 border">{el.total}</td>
                <td className="p-2 border">{el.available_trainee}</td>
                {/* <td className="p-2 border w-10">
                  <Button
                    size="sm"
                    type="button"
                    theme="primary-light"
                    onClick={setSelectedUpdate.bind(this, el)}
                  >
                    Update
                  </Button>
                </td> */}
                <td className="p-2 border w-10">
                  <Button
                    onClick={DeleteSchoolOnTheList.bind(this, el)}
                    size="sm"
                    type="button"
                    theme="danger-light"
                    disabled={delete_loading !== null}
                    loading={delete_loading === el.school_code}
                  >
                    Remove
                  </Button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </DashboardContainer>
    </>
  );
};
export default RequestTrainingSchools;
