import React from "react";
import { connect } from "react-redux";
import {
  Auth,
  FC_AddTraineeCategory,
  FC_AddTrainingInstitutionToTraining,
  FC_DeleteGroup,
  FC_DeleteTraineeCategoryToTraining,
  FC_DeleteTrainingInstitutionToTraining,
  FC_GetTrainingDetails,
  FC_SaveStepOneData,
  FC_UpdateRoomCapacity_Interface,
  GroupItem,
  MaterialLinks,
  TraineeCriterial,
  TrainingInterface,
  TrainingSettings_Interface,
  TrainingTrainers,
  TRNG_TRAINEE_VIEW_URL,
  TRNG_TRAINER_INSTITUTION_VIEW_URL,
} from "../../actions";
import Alert from "../../components/Alert/Alert";
import Button from "../../components/Buttons/Button";
import DashboardContainer from "../../components/DashboardContainer/DashboardContainer";
import Heading from "../../components/Heading/Heading";
import Loading from "../../components/Loading/Loading";
import { StoreState } from "../../reducers";
import AddCenterToTraining from "./AddCenterToTraining";
import AddSettingsToTraining from "./AddSettingsToTraining";
import CardTrainingRequest from "./CardTrainingRequest";
import StepOneBasicInfo from "./StepOneBasicInfo";
import StepTwoCentersTrainers from "./StepTwoCentersTrainers";
import { DATE } from "../../utils/functions";
import Step3Criteria from "./Step3Criteria";
import Step4Resource from "./Step4Resource";

// props for the component
interface AppProps {
  auth: Auth;
  training_id: string;
  close: () => void;
}

interface AlertInterface {
  target:
    | "MAIN-ERROR"
    | "MAIN-SUCCESS"
    | "SUCCESS-ADD-TRAINING-INSTITUTION"
    | "ERROR-ADD-TRAINING-INSTITUTION"
    | "SUCCESS-ADD-TRAINEE-CATEGORY"
    | "ERROR-ADD-TRAINEE-CATEGORY"
    | "ERROR-REMOVE-TRAINEE-CATEGORY"
    | "SUCCESS-REMOVE-TRAINEE-CATEGORY"
    | "SUCCESS-REMOVE-TRAINING-INSTITUTION"
    | "ERROR-REMOVE-TRAINING-INSTITUTION"
    | null;
  msg: string;
}

const defaultAlert: AlertInterface = {
  target: null,
  msg: "",
};

interface AppState {
  loading: boolean;
  step: number;
  loading_submit: boolean;
  training: TrainingInterface | null;
  alert: AlertInterface;

  // Training institution
  add_training_institution: boolean;
  loading__add_training_institution: boolean;
  delete_training_institution: number | string | null;

  // Trainee category
  add_trainee_category: boolean;
  loading__add_trainee_category: boolean;
  delete_trainee_category: number | string | null;
  loadingStepOneSave: boolean;

  // first step data
  step_one__title: string;
  step_one__description: string;
  step_one__start_date: string;
  step_one__end_date: string;
  step_one__sub_area: string;
  step_one__budget: string;
  step_one__topic: string;

  //** Step 2
  addCenter: boolean;
  addCenter_loading: boolean;
  delete_group_loading: null | string;
  open_add_group: GroupItem | null;
  loading_add_group: boolean;
  loading_materials: boolean;

  delete_loading: string | null;
}

class _RequestTraining extends React.Component<AppProps, AppState> {
  constructor(props: AppProps) {
    super(props);

    this.state = {
      loading: true,
      step: 1,
      loading_submit: false,
      training: null,
      alert: defaultAlert,
      // ** Step 1
      // Training institution
      add_training_institution: false,
      delete_training_institution: null,
      loading__add_training_institution: false,
      // Trainee category
      add_trainee_category: false,
      loading__add_trainee_category: false,
      delete_trainee_category: null,
      loadingStepOneSave: false,

      // Input for step one
      step_one__title: "",
      step_one__description: "",
      step_one__start_date: "",
      step_one__end_date: "",
      step_one__sub_area: "",
      step_one__budget: "",
      step_one__topic: "",
      // ** Step 2
      addCenter: false,
      addCenter_loading: false,
      delete_group_loading: null,
      open_add_group: null,
      loading_add_group: false,

      loading_materials: false,
      delete_loading: null,
    };
  }

  componentDidMount(): void {
    if (!this.props.auth.stakeholder?.development_partner_id)
      return this.setState({
        loading: false,
        alert: {
          msg: "Failed to authenticate you",
          target: "MAIN-ERROR",
        },
      });

    FC_GetTrainingDetails(this.props.training_id, (status, data, error) => {
      if (status) {
        this.setState({
          loading: false,
          alert: defaultAlert,
          training: data,
          // Set default data
          step_one__title: data?.title || "",
          step_one__description: data?.description || "",
          step_one__start_date: DATE(data?.start_date + "", "YYYY-MM-DD"),
          step_one__end_date: DATE(data?.end_date + "", "YYYY-MM-DD"),
          step_one__sub_area: data?.sub_area_id || "",
          step_one__budget: data?.budget?.toString() || "",
          step_one__topic: data?.topic_id?.toString() + "",
        });
      } else {
        this.setState({
          loading: false,
          alert: {
            target: "MAIN-ERROR",
            msg: `Error: ${error}`,
          },
          training: data,
        });
      }
    });
  }

  cleanError() {
    setTimeout(() => {
      this.setState({ alert: defaultAlert });
    }, 3000);
  }

  updatedRoomCapacity = (data: FC_UpdateRoomCapacity_Interface) => {
    console.log({ update_room: data });

    if (!this.state.training || !this.state.training.groups) return;
    this.setState({
      training: {
        ...this.state.training,
        groups: this.state.training.groups.map((el) =>
          el.id.toString() === data?.id?.toString()
            ? { ...el, number_of_trainees: data.number_of_trainees }
            : el
        ),
      },
    });
  };

  //********************************************* STEP 1 **************************************** */
  setStepOne__title = (value: string) =>
    this.setState({ step_one__title: value });
  setStepOne__description = (value: string) =>
    this.setState({ step_one__description: value });
  setStepOne__start_date = (value: string) =>
    this.setState({ step_one__start_date: value });
  setStepOne__end_date = (value: string) =>
    this.setState({ step_one__end_date: value });
  setStepOne__sub_area = (value: string) =>
    this.setState({ step_one__sub_area: value });
  setStepOne__budget = (value: string) =>
    this.setState({ step_one__budget: value });
  setStepOne__topic = (value: string) =>
    this.setState({ step_one__topic: value });

  // Add training institution to training
  AddTrainingInstitution = (institutions: TrainingSettings_Interface[]) => {
    if (!this.state.training) return;
    if (!window.confirm("Are you sure you want to save changes")) return;

    this.setState({
      alert: defaultAlert,
      loading__add_training_institution: true,
    });

    FC_AddTrainingInstitutionToTraining(
      {
        training_id: this.state.training?.id,
        data: institutions,
      },
      (status, data, err) => {
        if (status === true) {
          this.setState({
            alert: {
              target: "SUCCESS-ADD-TRAINING-INSTITUTION",
              msg: "The new institution has been added",
            },
            training: this.state.training
              ? {
                  ...this.state.training,
                  trainer_institution: this.state.training.trainer_institution
                    ? [...this.state.training.trainer_institution, ...data]
                    : [...data],
                }
              : this.state.training,
            loading__add_training_institution: false,
          });

          this.cleanError();
          setTimeout(() => {
            this.setState({ add_training_institution: false });
          }, 3500);
        } else {
          this.setState({
            alert: {
              target: "ERROR-ADD-TRAINING-INSTITUTION",
              msg: `Error: ${err}`,
            },
            loading__add_training_institution: false,
          });
        }
        this.cleanError();
      }
    );
  };

  // Add trainee institution
  AddTraineeCategory = (institutions: TrainingSettings_Interface[]) => {
    if (!this.state.training) return;
    if (!window.confirm("Are you sure you want to save changes")) return;

    this.setState({
      alert: defaultAlert,
      loading__add_trainee_category: true,
    });
    FC_AddTraineeCategory(
      {
        training_id: this.state.training.id,
        data: institutions,
      },
      (status, data, err) => {
        if (status === true) {
          this.setState({
            alert: {
              target: "SUCCESS-ADD-TRAINEE-CATEGORY",
              msg: "The new trainee categories has been added",
            },
            training: this.state.training
              ? {
                  ...this.state.training,
                  trainee_category: this.state.training.trainee_category
                    ? [...this.state.training.trainee_category, ...data]
                    : [...data],
                }
              : this.state.training,
            loading__add_trainee_category: false,
          });

          this.cleanError();
          setTimeout(() => {
            this.setState({ add_trainee_category: false });
          }, 3500);
        } else {
          this.setState({
            alert: {
              target: "ERROR-ADD-TRAINEE-CATEGORY",
              msg: `Error: ${err}`,
            },
            loading__add_trainee_category: false,
          });
        }
      }
    );
  };

  // delete training institution
  DeleteTrainingInstitution = (id: string | number) => {
    if (!this.state.training) return;
    if (
      !window.confirm(
        "Are you sure you want to remove the institution to this training?"
      )
    )
      return;
    this.setState({
      delete_training_institution: id,
      alert: defaultAlert,
    });

    FC_DeleteTrainingInstitutionToTraining(
      { training_id: this.state.training.id, id: id.toString() },
      (status, msg) => {
        if (status) {
          this.setState({
            delete_training_institution: null,
            alert: {
              target: "MAIN-SUCCESS",
              msg: `Training institution has been removed to this training`,
            },
            training: this.state.training
              ? {
                  ...this.state.training,
                  trainer_institution: this.state.training.trainer_institution
                    ? this.state.training.trainer_institution.filter(
                        (el) => el?.id + "" !== id + ""
                      )
                    : this.state.training.trainer_institution,
                }
              : this.state.training,
          });
          this.cleanError();
        } else {
          this.setState({
            delete_training_institution: null,
            alert: {
              target: "MAIN-ERROR",
              msg: `Error: ${msg}`,
            },
          });
        }
      }
    );
  };

  // delete training institution
  DeleteTraineeCategory = (id: string | number) => {
    if (!this.state.training) return;
    if (
      !window.confirm(
        "Are you sure you want to remove the trainee category to this training?"
      )
    )
      return;
    this.setState({
      delete_trainee_category: id,
      alert: defaultAlert,
    });

    FC_DeleteTraineeCategoryToTraining(
      { training_id: this.state.training.id, id: id.toString() },
      (status, msg) => {
        if (status) {
          this.setState({
            delete_trainee_category: null,
            alert: {
              target: "MAIN-SUCCESS",
              msg: `Trainee category has been removed to this training`,
            },
            training: this.state.training
              ? {
                  ...this.state.training,
                  trainee_category: this.state.training.trainee_category
                    ? this.state.training.trainee_category.filter(
                        (el) => el.id?.toString() !== id.toString()
                      )
                    : this.state.training.trainee_category,
                }
              : this.state.training,
          });
        } else {
          this.setState({
            delete_trainee_category: null,
            alert: {
              target: "MAIN-ERROR",
              msg: `Error: ${msg}`,
            },
          });
        }
      }
    );
  };

  saveStepOneData = () => {
    if (!this.state.training) return;
    this.setState({
      loadingStepOneSave: true,
    });
    FC_SaveStepOneData(
      {
        agenda_file_name: "this is the file name",
        budget: +this.state.step_one__budget,
        description: this.state.step_one__description,
        development_partner_id:
          this.props.auth.stakeholder?.development_partner_id + "",
        end_date: this.state.step_one__start_date,
        id: this.state.training?.id,
        // material_links: this.state.training?.material_links,
        material_links: [{ title: "doc" }, { title: "" }],
        start_date: this.state.step_one__start_date,
        sub_area_id: this.state.step_one__sub_area,
        title: this.state.step_one__title,
        topic_id: this.state.step_one__topic,
        total: 0,
      },
      (status, err) => {
        if (status) {
          this.setState({
            loadingStepOneSave: false,
            alert: {
              target: "MAIN-SUCCESS",
              msg: "Data updated successfully",
            },
            step: 2,
          });
        } else {
          this.setState({
            loadingStepOneSave: false,
            alert: {
              target: "MAIN-ERROR",
              msg: `Error: ${err}`,
            },
          });
        }
        this.cleanError();
      }
    );
  };

  //********************************************* STEP 2 **************************************** */

  deleteCenterGroup = ({
    training_id,
    id,
  }: {
    training_id: string;
    id: string;
  }) => {
    if (!window.confirm("Are you sure you want to remove this center")) return;
    this.setState({
      delete_group_loading: id,
    });
    FC_DeleteGroup(id, training_id, (status, msg) => {
      if (status) {
        this.setState({
          delete_group_loading: null,
          alert: {
            target: "MAIN-SUCCESS",
            msg: `the center has bee removed`,
          },
          training:
            (this.state.training && {
              ...this.state.training,
              // centers: this.state.training.centers?.filter(
              //   (el) => el.id.toString() !== id
              // ),
              groups: this.state.training.groups?.filter(
                (el) => el.id.toString() !== id.toString()
              ),
            }) ||
            this.state.training,
        });
      } else {
        this.setState({
          delete_group_loading: null,
          alert: {
            target: "MAIN-ERROR",
            msg: `Error: ${msg}`,
          },
        });
      }
      this.cleanError();
    });
  };

  addGroupHandler = (center: GroupItem) => {
    if (!this.state.training) return;
    this.setState({ open_add_group: center, addCenter: true });
  };

  addRoomToCenterData = (data: GroupItem) => {
    if (this.state.training)
      this.setState({
        training: {
          ...this.state.training,
          groups: [...(this.state.training.groups || []), data],
        },
        open_add_group: null,
      });
  };

  //********************************************* STEP 3 ***********************************************/

  updateCriterial = (criterial: TraineeCriterial) => {
    if (this.state.training)
      this.setState({
        training: {
          ...this.state.training,
          trainee_criteria: { ...criterial },
        },
        open_add_group: null,
        step: 4,
      });
  };

  updateMaterials = (material: MaterialLinks[]) => {
    if (!this.state.training) return;
    this.setState({
      loading_materials: true,
    });

    FC_SaveStepOneData(
      {
        agenda_file_name: "this is the file name",
        budget: +this.state.step_one__budget,
        description: this.state.step_one__description,
        development_partner_id:
          this.props.auth.stakeholder?.development_partner_id + "",
        end_date: this.state.step_one__start_date,
        id: this.state.training?.id,

        material_links: material,
        start_date: this.state.step_one__start_date,
        sub_area_id: this.state.step_one__sub_area,
        title: this.state.step_one__title,
        topic_id: this.state.step_one__topic,
        total: 0,
      },
      (status, err) => {
        if (status) {
          this.setState({
            loading_materials: false,
            alert: {
              target: "MAIN-SUCCESS",
              msg: "Material links updated successfully",
            },
            training: this.state.training
              ? {
                  ...this.state.training,
                  material_links: material,
                }
              : this.state.training,
          });
        } else {
          this.setState({
            loading_materials: false,
            alert: {
              target: "MAIN-ERROR",
              msg: `Error: ${err}`,
            },
          });
        }
        this.cleanError();
      }
    );
  };

  uploadFileSuccess = (file: string) => {
    if (!this.state.training) return;
    this.setState({
      training: {
        ...this.state.training,
        agenda_file_name: file,
      },
    });
  };

  successSubmitTrainer = (data: TrainingTrainers) => {
    this.setState({
      training: this.state.training
        ? {
            ...this.state.training,
            trainers: this.state.training.trainers
              ? [...this.state.training.trainers, data]
              : this.state.training.trainers,
          }
        : this.state.training,
    });
  };

  removeTrainerSuccess = (id: string) => {
    this.setState({
      training: this.state.training
        ? {
            ...this.state.training,
            trainers: this.state.training.trainers
              ? this.state.training.trainers.filter((el) => el.user_id !== id)
              : this.state.training.trainers,
          }
        : this.state.training,
    });
  };

  render() {
    if (this.state.loading)
      return (
        <div className="py-8 text-center">
          <Loading />
        </div>
      );

    return (
      <>
        {/* Add a Training institution */}
        {this.state.add_training_institution === true && (
          <AddSettingsToTraining
            close={() =>
              this.setState({
                add_training_institution: false,
              })
            }
            path_url={TRNG_TRAINER_INSTITUTION_VIEW_URL}
            submit={this.AddTrainingInstitution}
            submit_loading={this.state.loading__add_training_institution}
            title={"Add Training institution(s)"}
            existing_data={this.state.training?.trainer_institution || []}
            error_msg={
              this.state.alert.target === "ERROR-ADD-TRAINING-INSTITUTION"
                ? this.state.alert.msg
                : ""
            }
            success_msg={
              this.state.alert.target === "SUCCESS-ADD-TRAINING-INSTITUTION"
                ? this.state.alert.msg
                : ""
            }
          />
        )}

        {/* Add a Training institution */}
        {this.state.add_trainee_category === true && (
          <AddSettingsToTraining
            close={() =>
              this.setState({
                add_trainee_category: false,
              })
            }
            path_url={TRNG_TRAINEE_VIEW_URL}
            submit={this.AddTraineeCategory}
            submit_loading={this.state.loading__add_trainee_category}
            title={"Add Trainee category"}
            existing_data={this.state.training?.trainee_category || []}
            error_msg={
              this.state.alert.target === "ERROR-ADD-TRAINEE-CATEGORY"
                ? this.state.alert.msg
                : ""
            }
            success_msg={
              this.state.alert.target === "SUCCESS-ADD-TRAINEE-CATEGORY"
                ? this.state.alert.msg
                : ""
            }
          />
        )}

        {/* Add a Training institution */}
        {this.state.addCenter === true && this.state.training && (
          <AddCenterToTraining
            training_id={this.state.training?.id}
            close={() =>
              this.setState({
                addCenter: false,
                open_add_group: null,
              })
            }
            selectedCenter={this.state.open_add_group}
            centers={this.state.training?.centers || []}
            successHandler={(center) =>
              this.setState({
                training:
                  (this.state.training && {
                    ...this.state.training,
                    groups: [...(this.state.training.groups || []), center],
                  }) ||
                  this.state.training,
              })
            }
          />
        )}

        {/* open_add_group */}
        {/* {this.state.open_add_group !== null && this.state.training && (
          <AddGroupToCenter
            successHandler={this.addRoomToCenterData}
            training_id={this.state.training?.id}
            center_id={this.state.open_add_group}
            close={() =>
              this.setState({
                open_add_group: null,
              })
            }
          />
        )} */}

        {this.state.alert.target === "MAIN-SUCCESS" && (
          <Alert theme="success" fixed>
            {this.state.alert.msg}
          </Alert>
        )}
        {this.state.alert.target === "MAIN-ERROR" && (
          <Alert theme="danger" fixed>
            {this.state.alert.msg}
          </Alert>
        )}
        <div className="lg:sticky top-3 z-50 ">
          <DashboardContainer>
            <div className="flex items-center">
              <div className="flex-1">
                <Heading
                  size="lg"
                  className=" py-2 lg:pb-3 lg:pt-2 font-light text-left text-gray-500"
                >
                  {this.state.training
                    ? `${
                        this.state.training.title ||
                        "Training application request"
                      }`
                    : "Training request"}
                </Heading>
              </div>

              {this.state.loading_submit === false && (
                <Button
                  size="md"
                  theme="danger-light"
                  onClick={() =>
                    window.confirm(
                      "Are you sure you want to stop the request process \n All your previous choices will be lost"
                    ) && this.props.close()
                  }
                >
                  Close
                </Button>
              )}
            </div>

            <div className="grid overflow-hidden grid-cols-2 lg:grid-cols-4 border rounded-md">
              {[
                {
                  step: 1,
                  title: "Basic info",
                  description: "Basic information",
                  active: true,
                },
                {
                  step: 2,
                  title: "Training area",
                  description: "Centers & groups & Trainers",
                },
                {
                  step: 3,
                  title: "Targeted group & Criterial",
                  description: "Users and criterial",
                },
                {
                  step: 4,
                  title: "Documents & Resources",
                  description: "Required documents",
                },
              ].map((itm, i) => (
                <CardTrainingRequest
                  onClick={() => {
                    this.setState({
                      step: itm.step,
                    });
                  }}
                  key={i}
                  active={itm.step === this.state.step}
                  description={itm.description}
                  number={itm.step}
                  theme={
                    itm.step === this.state.step
                      ? "yellow"
                      : this.state.step > itm.step
                      ? "green"
                      : "gray"
                  }
                  title={itm.title}
                  priority={itm.step <= this.state.step}
                />
              ))}
            </div>
          </DashboardContainer>
        </div>

        {this.state.step === 1 && this.state.training && (
          <StepOneBasicInfo
            loadingStepOneSave={this.state.loadingStepOneSave}
            sub_area_id={this.state.training.sub_area_id || ""}
            successData={this.saveStepOneData}
            trainee_category={
              this.state.training.trainee_category
                ? this.state.training.trainee_category
                : []
            }
            trainer_institutions={
              this.state.training.trainer_institution
                ? this.state.training.trainer_institution
                : []
            }
            addTraineeCategory={() =>
              this.setState({ add_trainee_category: true })
            }
            addTrainerInstitution={() =>
              this.setState({ add_training_institution: true })
            }
            loadingTraineeCategory={false}
            loadingTrainerInstitution={false}
            removeTraineeCategory={this.DeleteTraineeCategory}
            removeTrainerInstitution={this.DeleteTrainingInstitution}
            loadingTraineeCategoryRemove={this.state.delete_trainee_category}
            loadingTrainersInstitutionRemove={
              this.state.delete_training_institution
            }
            step_one__title={this.state.step_one__title}
            step_one__description={this.state.step_one__description}
            step_one__start_date={this.state.step_one__start_date}
            step_one__end_date={this.state.step_one__end_date}
            step_one__sub_area={this.state.step_one__sub_area}
            step_one__budget={this.state.step_one__budget}
            setStepOne__title={this.setStepOne__title}
            setStepOne__description={this.setStepOne__description}
            setStepOne__start_date={this.setStepOne__start_date}
            setStepOne__end_date={this.setStepOne__end_date}
            setStepOne__sub_area={this.setStepOne__sub_area}
            setStepOne__budget={this.setStepOne__budget}
            step_one__topic={this.state.step_one__topic}
            setStepOne__topic={this.setStepOne__topic}
          />
        )}

        {this.state.step === 2 && this.state.training && (
          <StepTwoCentersTrainers
            training={this.state.training}
            addCenter={() =>
              this.setState({
                addCenter: true,
              })
            }
            deleteCenterGroup={this.deleteCenterGroup}
            delete_group_loading={this.state.delete_group_loading}
            addGroupHandler={this.addGroupHandler}
            updatedRoomCapacity={this.updatedRoomCapacity}
            prevStep={() => this.setState({ step: 1 })}
            nextStep={() => {
              // if (TrainingValidation__Step1(this.state.training))
              this.setState({ step: 3 });
            }}
            successSubmitTrainer={this.successSubmitTrainer}
            removeTrainerSuccess={this.removeTrainerSuccess}
          />
        )}

        {this.state.step === 3 && this.state.training && (
          <Step3Criteria
            training={this.state.training}
            successCriterial={this.updateCriterial}
            prevStep={() => this.setState({ step: 2 })}
            nextStep={() => this.setState({ step: 4 })}
          />
        )}

        {this.state.step === 4 && this.state.training && (
          <Step4Resource
            training={this.state.training}
            submitTrainingResource={this.updateMaterials}
            loading_materials={this.state.loading_materials}
            uploadFileSuccess={this.uploadFileSuccess}
            prevStep={() => this.setState({ step: 2 })}
            stake_holder_id={
              this.props.auth.stakeholder?.development_partner_id + ""
            }
          />
        )}
      </>
    );
  }
}

const mapStateToProps = ({ auth }: StoreState): { auth: Auth } => {
  return { auth: auth };
};

export const RequestTraining = connect(mapStateToProps, {})(_RequestTraining);
