import React, { FC, ReactElement, useEffect, useState } from "react";
import {
  MdCheckBox,
  MdCheckBoxOutlineBlank,
  MdCheckCircle,
} from "react-icons/md";
import Alert from "../../components/Alert/Alert";
import Button from "../../components/Buttons/Button";
import Loading from "../../components/Loading/Loading";
import Modal from "../../components/Modal/Modal";
import {
  FC_GetSettingType,
  TrainingSettings_Interface,
} from "../actions/training.settings.action";

type AddSettingsToTrainingProps = {
  close: () => void;
  title: string;
  existing_data: TrainingSettings_Interface[];
  path_url: string;
  submit: (newData: TrainingSettings_Interface[]) => void;
  submit_loading: boolean;
  success_msg: string;
  error_msg: string;
};

const AddSettingsToTraining: FC<AddSettingsToTrainingProps> = ({
  close,
  title,
  existing_data,
  path_url,
  submit,
  submit_loading,
  success_msg,
  error_msg,
}): ReactElement => {
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState("");
  const [data, setData] = useState<TrainingSettings_Interface[]>([]);
  const [selectedItems, setSelectedItem] = useState<
    TrainingSettings_Interface[]
  >([]);

  useEffect(() => {
    if (loading)
      FC_GetSettingType(path_url, (status, res_data, msg) => {
        setLoading(false);
        if (!status) setError(`Error: ${msg}`);
        setData(res_data);
      });
  }, [data, path_url, loading]);

  const AddRemoveSelected = (selected: TrainingSettings_Interface) => {
    if (submit_loading) return null;
    const existItem = selectedItems.find(
      (el) => el.id?.toString() === selected.id?.toString()
    );

    if (!existItem) {
      setSelectedItem([...selectedItems, selected]);
    } else {
      setSelectedItem(
        selectedItems.filter(
          (ox) => ox.id?.toString() !== selected.id?.toString()
        )
      );
    }
  };

  return (
    <div>
      <Modal
        backDrop={true}
        close={close}
        backDropClose={false}
        displayClose={true}
        title={title}
        padding={{
          title: true,
          body: undefined,
          footer: undefined,
        }}
      >
        <div className="border-t p-2 px-3">
          {success_msg !== "" && <Alert theme="success">{success_msg}</Alert>}
          {error_msg !== "" && <Alert theme="danger">{error_msg}</Alert>}
          <p className="text-center text-gray-500 pb-2">
            Choose one or more items
          </p>
          {error !== "" && <Alert theme="danger">{error}</Alert>}
          {loading === true && (
            <div className="py-8 text-center">
              <Loading />
            </div>
          )}

          {data.map((el, i) => {
            const foundItem = existing_data.find(
              (itm) => itm.id?.toString() === el.id?.toString()
            );

            const selectedItem = selectedItems.find(
              (itm) => itm.id?.toString() === el.id?.toString()
            );

            return (
              <div
                key={i}
                className={`${
                  selectedItem ? "bg-blue-50" : "bg-white"
                }  border rounded p-2 mb-2 flex items-center cursor-pointer ${
                  foundItem ? "opacity-60" : ""
                }`}
                onClick={() => {
                  if (!foundItem) AddRemoveSelected(el);
                }}
              >
                <div className="text-3xl pr-2">
                  {foundItem ? (
                    <b className="text-green-300">
                      <MdCheckCircle />
                    </b>
                  ) : selectedItem ? (
                    <span className="text-blue-600">
                      <MdCheckBox />
                    </span>
                  ) : (
                    <b className="text-gray-700">
                      <MdCheckBoxOutlineBlank />
                    </b>
                  )}
                </div>
                <div className="flex-1">
                  <h4 className="font-bold">{el.name}</h4>
                  {el?.description && (
                    <p className="text -mt-1">{el?.description}</p>
                  )}
                </div>
                <div>
                  {foundItem && (
                    <span className="text-xs px-1 py-0.5 bg-green-200 font-bold text-green-800 rounded-full">
                      Selected
                    </span>
                  )}
                </div>
              </div>
            );
          })}

          {data.length <= 0 && loading === false && (
            <div>
              <Alert theme="info">No results found!</Alert>
            </div>
          )}

          {selectedItems.length >= 1 && success_msg === "" && error_msg === "" && (
            <div className="text-right pt-3">
              <Button
                onClick={submit.bind(this, selectedItems)}
                theme="success"
                size="lg"
                loading={submit_loading}
              >
                Add {selectedItems.length} items the list
              </Button>
            </div>
          )}
        </div>
        {data.length >= 1 && (
          <div className="py-3 border-t">
            <h4 className="text-xl text-center font-bold">Can't find it?</h4>
            <p className="text-center text-sm text-gray-500 pb-2">
              If you can't find the item you are looking for <br />
              Contact REB, they will add it here
            </p>
          </div>
        )}
      </Modal>
    </div>
  );
};
export default AddSettingsToTraining;
