import React, { FC, ReactElement } from "react";
import Button from "../../components/Buttons/Button";

type CriteriaContainerProps = {
  title: string;
  title_key: string;
  description: string;
  addButton?: () => void;
  disable: boolean;
};

const CriteriaContainer: FC<CriteriaContainerProps> = (props): ReactElement => {
  const { title, title_key, description, addButton, disable } = props;
  return (
    <div className="p-2 border rounded bg-white">
      <div className="flex items-center border-b pb-1">
        <section className="flex-1">
          <h3 className="text-lg font-bold">{title}</h3>
          <p className="text-sm text-gray-500">{description}</p>
        </section>
        <section>
          {addButton && (
            <Button onClick={addButton} theme="accent-light" disabled={disable}>
              Add {title_key}
            </Button>
          )}
        </section>
      </div>
      <div className="py-2 h-44 overflow-auto">{props.children}</div>
    </div>
  );
};
export default CriteriaContainer;

type CriterialListProps = {
  el: number;
  id: string;
  title: string;
  removeItem: (id: string) => void;
  loadingRemove: string | null;
};

export const CriteriaItems: FC<CriterialListProps> = (props): ReactElement => {
  const { title, removeItem, el, loadingRemove, id } = props;
  return (
    <div className="flex p-1 mb-2 bg-gray-100 rounded border items-center">
      <section className="pr-2 text-gray-400">{el}</section>
      <section className="flex-1 font-bold">
        <h4>{title}</h4>
      </section>
      <section>
        <Button
          size="sm"
          theme="danger-light"
          disabled={loadingRemove !== null}
          loading={loadingRemove === id.toString() ? true : false}
          onClick={removeItem}
        >
          Remove
        </Button>
      </section>
    </div>
  );
};
