import React, { FC, ReactElement } from "react";
import { MdWarning } from "react-icons/md";
import Button from "../../components/Buttons/Button";
import { DATE } from "../../utils/functions";
import { TrainingsItem } from "../actions/training.settings.action";

type TrainingListItemProps = {
  training: TrainingsItem;
  delete_loading?: string | null;
  update_loading?: string | null;
  viewDetails?: (id: string | number) => void;
  editTraining?: (id: string | number) => void;
  deleteTraining?: (id: string | number) => void;
  i: number;
};

const TrainingListItem: FC<TrainingListItemProps> = ({
  training,
  viewDetails,
  editTraining,
  deleteTraining,
  delete_loading,
  update_loading,
  i,
}): ReactElement => {
  if (
    !training.title &&
    (!training.implementation_status ||
      training.implementation_status === "DRAFT")
  )
    return (
      <tr>
        <td className="text-center border">{i + 1}</td>
        <td className="bg-red-50 bg-opacity-50 p-3 border" colSpan={12}>
          <div className="flex items-center">
            <div className="text-4xl pr-3 text-accent-900">
              <MdWarning />
            </div>
            <div className="flex-1">
              <h4 className="font-bold text-accent-900">
                This training application is missing some info
              </h4>
              <h4 className="text-sm">
                Please click on <u>Edit training application</u> and add more
                information or delete the draft if you created it accidentally.{" "}
              </h4>
            </div>
            <div className="pr-4">
              {editTraining && (
                <Button
                  size="sm"
                  onClick={editTraining.bind(this, training.id)}
                  disabled={update_loading !== null}
                  loading={
                    update_loading?.toString() === training.id.toString()
                      ? true
                      : false
                  }
                >
                  Edit training application
                </Button>
              )}
            </div>
            <div className="">
              {deleteTraining && (
                <Button
                  size="sm"
                  onClick={deleteTraining.bind(this, training.id)}
                  theme="danger-light"
                  disabled={delete_loading !== null}
                  loading={
                    delete_loading?.toString() === training.id.toString()
                      ? true
                      : false
                  }
                >
                  Delete training request
                </Button>
              )}
            </div>
          </div>
        </td>
      </tr>
    );
  return (
    <tr
      key={training.id}
      onClick={viewDetails?.bind(this, training.id)}
      className="text-left hover:bg-yellow-50 cursor-pointer"
    >
      <td className="p-2 border">{i + 1}</td>
      <td className="p-2 border">{training?.title}</td>
      <td className="p-2 border">{training?.development_partner_name}</td>
      <td className="p-2 border">{training?.intervention_area}</td>
      <td className="p-2 border">{DATE(training?.start_date)}</td>
      <td className="p-2 border">{DATE(training?.end_date)}</td>
      <td className="p-2 border text-center">
        <label
          className={`px-2 py-1 font-bold  ${
            training.evaluation_status === "APPROVED"
              ? "text-green-900 bg-green-100"
              : "text-accent-900 bg-accent-500"
          } text-xs rounded-full`}
        >
          {training.implementation_status === "DRAFT"
            ? training.implementation_status
            : training.evaluation_status}
        </label>
      </td>
    </tr>
  );
};
export default TrainingListItem;
