import React, { FC, ReactElement } from "react";
import { IconType } from "react-icons";
import { MdDelete, MdModeEdit } from "react-icons/md";
import Button from "../../components/Buttons/Button";
import { TrainingSettings_Interface } from "../actions/training.settings.action";

type SelectGroupProps = {
  Icon: IconType;
  title: string;
  description: string;
  onCreate?: () => void;
  create_btn_label?: string;
  onUpdate?: () => void;
  onDelete?: (id: string | number) => void;
  data: TrainingSettings_Interface[];
  createLading?: boolean;
  updateLading?: null | string | number;
  deleteLoading?: null | string | number;
  error?: string;
};

const SelectGroup: FC<SelectGroupProps> = ({
  Icon,
  title,
  description,
  onCreate,
  create_btn_label,
  onUpdate,
  onDelete,
  data,
  createLading,
  deleteLoading,
  updateLading,
  error,
}): ReactElement => {
  /* function body */
  return (
    <div
      className={`rounded bg-gray-200  flex-1 ${
        error !== "" ? "border-2 border-red-500" : "border"
      }`}
    >
      <div className="flex items-center p-2 border-b bg-gray-50">
        <div className="text-5xl pr-2 text-blue-900 text-opacity-80">
          <Icon />
        </div>
        <div className="flex-1">
          <h1 className="text-lg font-bold">{title}</h1>
          <p className="text-gray-500 -mt-1 text-sm">{description}</p>
        </div>
        <div className="">
          {create_btn_label && onCreate && (
            <Button
              type="button"
              theme="primary-light"
              onClick={onCreate}
              loading={createLading}
            >
              {create_btn_label}
            </Button>
          )}
        </div>
      </div>
      <div className="p-2 bg-gray-200">
        {data.length === 0 && (
          <div className="py-4 text-center">
            <h6 className="inline text-8xl text-gray-500">
              <Icon className="mx-auto" />
            </h6>
            <h4 className="text-lg -mt-3 text-gray-500">
              Nothing yet selected!
            </h4>
          </div>
        )}
        {data.map((trn_cat, i) => {
          return (
            <div
              key={i}
              className="p-1 border bg-white hover:bg-blue-100 rounded mb-2 items-center hover:border-blue-600 flex"
            >
              <section className="px-2 text-blue-900 text-opacity-70 text-xl">
                #{i + 1}
              </section>
              <section className="flex-1">
                <h1 className="text-lg font-bold">{trn_cat.name}</h1>
                <p className="-mt-1 text-gray-500 text-sm">{trn_cat.name}</p>
              </section>
              {onUpdate && trn_cat.id && (
                <section className="pr-2">
                  <Button
                    size="sm"
                    theme="primary-light"
                    type="button"
                    onClick={onUpdate.bind(this, trn_cat.id)}
                    loading={
                      updateLading
                        ? updateLading.toString() === trn_cat.id.toString()
                          ? true
                          : false
                        : false
                    }
                    disabled={
                      updateLading !== null && updateLading !== undefined
                    }
                  >
                    <section className="text-2xl" title="Update this item">
                      <MdModeEdit />
                    </section>
                  </Button>
                </section>
              )}
              {onDelete && trn_cat.id && (
                <section className="pr-2">
                  <Button
                    onClick={onDelete.bind(this, trn_cat.id)}
                    size="sm"
                    theme="danger-light"
                    type="button"
                    loading={
                      deleteLoading
                        ? deleteLoading.toString() === trn_cat.id.toString()
                          ? true
                          : false
                        : false
                    }
                    disabled={
                      deleteLoading !== null && deleteLoading !== undefined
                    }
                  >
                    <div className="text-2xl" title="Delete this item">
                      <MdDelete />
                    </div>
                  </Button>
                </section>
              )}
            </div>
          );
        })}
      </div>
      {error !== "" && <p className="text-center text-red-500">{error}</p>}
    </div>
  );
};
export default SelectGroup;
