import React, { ChangeEvent, FC, ReactElement } from "react";
import { MdSearch } from "react-icons/md";
import { InputSize, InputSizeType } from "./InputTypes";

type SearchInputProps = {
  onChange: (event: ChangeEvent<HTMLInputElement>) => void;
  value: string | number;
  placeholder: string;
  disabled?: boolean;
  name: string;
  className?: string;
  size?: InputSizeType;
  Input_className?: string;
  InputContainer_className?: string;
  MainContainer_className?: string;
  max?: number;
};

const SearchInput: FC<SearchInputProps> = ({
  onChange,
  value,
  placeholder,
  disabled = false,
  name,
  Input_className = "",
  InputContainer_className = "",
  MainContainer_className = "",
  size = "md",
  max,
}): ReactElement => {
  /* function body */
  return (
    <>
      <div className={`w-full my-2 ${MainContainer_className}`}>
        <div className={`rounded relative ${InputContainer_className} `}>
          <div className="flex p-1 px-2 rounded-lg items-center border bg-gray-100">
            <label htmlFor={name} className="text-3xl text-gray-700">
              <MdSearch />
            </label>
            <input
              type={"search"}
              name={name}
              id={name}
              placeholder={placeholder}
              onChange={onChange}
              className={` outline-none bg-transparent flex-1
                w-full text-lg 
                ${InputSize(size)}
                px-3 ${Input_className}`}
              value={value}
              disabled={disabled}
              maxLength={max}
            />
          </div>
        </div>
      </div>
    </>
  );
};
export default SearchInput;
